import React from 'react';
import {Link} from 'react-router-dom'



export const Paragraph = props => {
    let {text, 
        readMore = false,
        url,
        readMoreText = 'Read more',
        isAboutUs = false,
    } = props;
    return (
        <span>
            {
                isAboutUs ? <p className='about-description-text'>
                <span>
                    {text}
                </span><br/>
                {
                    readMore && <Link to={url} className='home-read-more-link'>{readMoreText}</Link>
                }
                </p>: <p className='home-description-text mb-5'>
                    <span>
                        {text}
                    </span><br/>
                {
                    readMore && <Link to={url} className='home-read-more-link'>{readMoreText}</Link>
                }
                </p>

            }
        </span>
    )
}

export const HeaderLink = props => {
    let {url, text} = props;
    return (
        <Link to={url} className='home-header-link mb-2'>{text}</Link>
    )
}

export const AboutUsHeader = props => {
    let {text} = props;
    return (
        <h3 className='about-header-link mb-2'>{text}</h3>
    )
}

export const TextHeader = props => {
    let { url, text } = props;
    return (
        <h3 to={url} className='home-header-link mb-2 h6' style={{color:'black'}}>{text}</h3>
    )
}