import React, { Component } from 'react';
import './style.css';
import {connect} from 'react-redux';
import {resetPassword} from './../../../action/settings';
import {Redirect} from 'react-router-dom';




let emailRegx = RegExp(/^[A-z0-9.!#$%&'*+/=?^_`{|}~-]+@[A-z0-9-]+(?:\.[A-z0-9-]+)*$/);

class ForgotPassword extends Component {
    constructor(){
        super()
        this.state ={
            email:'',
            errorMessage:{
                email:''
            }
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleChange(e){
    let {name,value} = e.target
    let {errorMessage} = this.state
    switch (name) {
        case 'email':
            errorMessage.email = emailRegx.test(value)?'':'invalid email';
            break;
    
        default:
            break;
    }
    this.setState({[name]:value,errorMessage})
    }
    validateError = ()=>{
        let {errorMessage,email} = this.state;
        let isValid = true;

        Object.values(errorMessage).forEach(value=>{
            value.length > 0 && (isValid = false) 
        });
        Object.values({email}).forEach(value=>{
            value === '' && (isValid = false) 
        });
        return isValid
    }
    handleSubmit(e){
        e.preventDefault()
        if(this.validateError()){
            this.props.resetPassword({email:this.state.email})
        }
    }
    handleBackward(e){
        e.preventDefault()
        this.props.history.goBack()
    }
    render() {
        let {errorMessage} = this.state
        if(this.props.email && this.props.message.length === 0) return <Redirect to='/reset-password/token'/>
        return (
            <div className='container mx-auto px-0'>
                <div className='row login-container mx-0'>
                    <form onSubmit={this.handleSubmit} className='m-auto login-form col-12 col-md-8 col-lg-5 pt-5 row pb-4 '>
                        <h3 className='signup-form text-center col-12 fadeIn'>Password Reset</h3>
                        <p className='small text-danger bold text-center mx-auto'>{this.props.message}</p>
                        <div className='password-input-container col-12 mt-3'>
                            {/* <label for='userName mb-0'>User name</label> */}
                            <input name='email' className={`${errorMessage.email.length > 0?'text-danger':''} form-control`} placeholder='Email' onChange={this.handleChange} type='email' />
                            <span className='contact-error'>{errorMessage.email.length > 0 ? errorMessage.email:''}</span>
                            <button onClick={this.handleSubmit} className='small login-submit bg-info mt-4 py-2 px-3 mx-auto text-rigth w-100' type='submit'>Submit</button>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

const mapStateToProps  = (state)=>{
    return{
        message:state.passwordReducer.message,
        email:state.passwordReducer.email
    }
}


export default connect(mapStateToProps , {resetPassword})(ForgotPassword)