import React, { Component } from 'react'
import Navbar from './Navbar'
import {LoadUser} from './../../../action/auth'
import {connect} from 'react-redux'

class NavContainer extends Component {
    componentWillMount(){
        if(!this.props.auth){
            this.props.LoadUser()
        }
    }
    constructor(){
        super()
        this.state = {
            toggle:''
        }
        this.handleToggle = this.handleToggle.bind(this)
    }
    handleToggle(){
        this.state.toggle === ''?this.setState({toggle:'d-block'}):this.setState({toggle:''})
    }
    render() {
        return (
            <div>
                <Navbar auth={this.props.auth} {...this.state} handleToggle={this.handleToggle} userInfo={this.props.userInfo}/>
            </div>
        )
    }
}

const mapStateToProps = (state)=>{
    let isValid = state.auth.log === {} ? null : state.auth.log.success 
    return {
        auth: isValid,
        userInfo: isValid ?state.auth.log.response: null

    }
}


export default connect(mapStateToProps, { LoadUser})(NavContainer)