import React, { Component } from 'react'
import Slider from "react-slick";


import image from './../../../../img/f1.jpg'
import image2 from './../../../../img/f2.jpg'
import image3 from './../../../../img/f3.jpg'
import image4 from './../../../../img/f4.jpg'
import { Paragraph, HeaderLink } from '../../../Template';
import Link from 'react-router-dom/Link';

export default class LargeSideContainer extends Component {
    render() {
    let settings = {
      dots: true,
      infinite: true,
      speed: 300,
      slidesToShow: 1,
      slidesToScroll: 1,
      vertical: false,
      autoplay:true,
      arrows:true,
     
    };
        return (
            <div>
                <section className='w-100 large-side-container'>
                    <Slider {...settings}>
                        <div className='homepage-header-img'>
                            <img src={image} alt='company name' className='w-100'/>
                        </div>
                        <div  className='homepage-header-img'>
                            <img src={image2} alt='company name' className='w-100'/>
                        </div>
                        <div  className='homepage-header-img'>
                            <img src={image3} alt='company name' className='w-100'/>
                        </div>
                        <div  className='homepage-header-img'>
                            <img src={image4} alt='company name' className='w-100'/>
                        </div>
                    </Slider>
                </section>
                <section className='pt-5'>
                    
                    {/* Welcome to Fast Helpers */}
                    <HeaderLink url={'/about-us'} text={'Welcome to Fast Helpers'}/>
                    <Paragraph isAboutUs={true} text={'Are you wondering how to raise funds to kick start your dream project, further your education, live your dream life or cater for that essential need? You need not worry.'}/>
                    <Paragraph isAboutUs={true} text={'FASTHELPERS GROUP-GHANA is here to bring to your doorsteps the dream life with comfort you have ever envisaged to live.'} />
                    <Paragraph isAboutUs={true} text={'All you need is a GH 100 to launch you into a realm of endless wealth-making. YES, just a onetime payment of GH100 will change your life for good.'} />
                    <Paragraph readMore={true} url={'/howitworks'} text={'With us, you dont need any educational qualification to be a member. All you need is your dedication and commitment and you will arrive at your dream in no time.'} />
                    <Link to='/signup' className='d-block mb-5 btn btn-primary px-5'>Register Now</Link>

                    {/* What is Fast Helpers */}
                    <HeaderLink url={'/about-us'} text={'What is Fast Helpers'} />
                    <Paragraph readMore={true} url={'/about-us'} text={'Fast Helpers Global Group is a Financial Networking System, Health Care and Training group which has been created to help people acquire financial freedom and good health care and to become entrepreneurs, helping them to raise capital to start their own businesses through the power of networking.'} />
                    
                    {/* Mission */}
                    <HeaderLink url={'/about-us'} text={'Our Mission'}/>
                    <Paragraph text={'Raising ordinary people to become financially empowered through financial literacy and the creation of wealth with the aid of a credible and trusted system, and working together as one with a common goal.'}/>

                    {/* Vision */}
                    <HeaderLink url={'/about-us'} text={'Our Vision'} />
                    <Paragraph text={'To become a leading association in Ghana and beyond, ensuring financial independence of its members in the near future, and relying on committed leaders, motivated members and a trusted system.'} />
                </section>
            </div>
        )
    }
}
