import React, { Component } from 'react'
import { InputWithLable, InputDropDown } from '../Template'



class AccountProfile extends Component {
    render() {
        let {
            networkService,
            momoNumber,
            accountNumber,
            bankName,
            branch,
            accountName,
            notEdit,
            handleChange,
            borderClass,
            handleToggleEdit,
            handleToggleEditCancel,
        } = this.props
        return (
            <div className='w-100'>
                <div className='container mx-auto'>
                    <div className='mt-4 d-flex'>
                        <button className='ml-auto btn btn-info' onClick={handleToggleEdit}>
                            {
                                notEdit ? <i className='fa fa-edit '> Edit</i> : <i className='fa fa-save '> Save</i>
                            }
                        </button>
                        {
                            !notEdit && <button className='ml-3 btn btn-danger' onClick={handleToggleEditCancel}><i className='fa fa-times'></i> Cancel</button>
                        }

                    </div>
                    <div className='mb-5'>
                        <h3 className='h5 text-success ml-lg-5'>Mobile Money</h3>
                        <InputDropDown borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={networkService} label='Network Service' name='networkService' placeholder={'Enter Network Service'} >
                            <option value='mtn'>MTN Mobile Money</option>
                            <option value='vodaphone'>Vodaphone cash</option>
                            <option value='tigo'>Tigo Cash</option>
                        </InputDropDown>
                        <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={momoNumber} label='MoMo Number' name='momoNumber' placeholder={'Enter MoMo Number'} />
                    </div>
                    <div >
                        <h3 className='h5 text-primary ml-lg-5'>Bank Details</h3>
                        <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={bankName} label='Bank Name' name='bankName' placeholder={'Bank Name'} />
                        <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={branch} label='Branch' name='branch' placeholder={'Branch'} />
                        <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={accountName} label='Account Name' name='accountName' placeholder={'Account Name'} />
                        <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={accountNumber} label='Account Number' name='accountNumber' placeholder={'Enter Account Number'} />
                    </div>

                </div>
            </div>
        )
    }
}

export default AccountProfile
