import React, { Component } from 'react';
import Profile from './Profile';
import {connect} from 'react-redux';
import {Redirect} from 'react-router-dom'
import {updateProfile} from './../../../../action/userAction'
import {upadteUserProfile} from './../../../../action/settings'
import {LoadUser} from './../../../../action/auth'



 class ProfileContainter extends Component {
    componentWillMount(props){
        let {auth} = this.props;
        if(!auth){
            // LOAD USE INFO FROM THE DATABASE
            this.props.LoadUser()
        }
    }
    componentDidMount(){
        this.setState({...this.props.userInfo});
    }
    constructor(props){
        super(props);
        this.state = {
            firstName:'',
            lastName:'',
            email: '',
            phone: '',
            country:'',
            region: '',
            town: '',
            userName:'',
            notEdit:true,
            editImage: false,
            borderClass:'myinput-border-none',
            ...this.props.isAuth
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleToggleEdit = this.handleToggleEdit.bind(this);
        this.handleToggleEditCancel = this.handleToggleEditCancel.bind(this);
        this.handleChangeImage = this.handleChangeImage.bind(this);
    }
    handleChange(e){
        e.preventDefault();
        let {name , value} = e.target;
    
        return this.setState({[name]:value})
    }

    handleChangeImage(e){
        if(!this.state.editImage){
            return this.setState({editImage:true})
        }
        return this.setState({editImage:false})

    }
    handleToggleEdit(){
        let {firstName , lastName , email , phone, country,region,town,userName} = this.state
        let update = {
            firstName,
            lastName,
            email,
            phone,
            country,
            region,
            town,
            userName
        }
        this.state.notEdit? this.setState({
            borderClass:'',
            notEdit: false,
            prevState: {...this.state,borderClass:'myinput-border-none',notEdit: true}}) :this.setState({
                borderClass:'myinput-border-none',
                notEdit: true},()=>{
            return this.props.upadteUserProfile(update);
        })
        
    }
    handleToggleEditCancel(){
        this.setState({...this.state.prevState})
    }
    render() {
        if(!this.props.auth) return <Redirect to={'/login'}/>
        return (
            <div className='px-3 pb-5'>
                <Profile handleChangeImage={this.handleChangeImage} {...this.state} handleChange={this.handleChange} handleToggleEdit={this.handleToggleEdit} handleToggleEditCancel={this.handleToggleEditCancel} />
            </div>
        )
    }
}



const mapStateToProps  = (state , props)=>{
    let isValid = state.auth.log === {} ? null : state.auth.log.success 
    return {
        auth: isValid,
        userInfo: isValid ? state.auth.log.response: null
    }
}
export default connect(mapStateToProps, {updateProfile , upadteUserProfile , LoadUser})(ProfileContainter)