import {TOKEN_MAIL_SENT_FAiLED,TOKEN_MAIL_SENT,TOKEN_SENT_SUCCESSFUL,PASSWORD_SET_FAILED,DASHBOARD_REST_SUCCESSFUL, DASHBOARD_REST_FAILED } from './../type';



const passwordReducer = ( preState={email:null , message:'', token:null},action)=>{
    switch (action.type) {
        case TOKEN_MAIL_SENT:
            return {
                message: action.payload.message ,
                email: action.payload.email,
                token:null
            };
        case TOKEN_MAIL_SENT_FAiLED:
            return {
                message: action.payload.message,
                email: null,
                token:null
            };
        case TOKEN_SENT_SUCCESSFUL:
            return {
                message: action.payload.message,
                email: action.payload.email,
                token:action.payload.token
            };
        case PASSWORD_SET_FAILED:
             return {
                message: action.payload.message,
                email: action.payload.email,
                token:action.payload.token
            };
        case DASHBOARD_REST_SUCCESSFUL:
            return{
                message:'',
                email:'',
                token:'',
                oldPassword: action.payload.oldPassword,
                success: action.payload.success,
                updated: action.payload.updated

            }
        case DASHBOARD_REST_FAILED:
            return{
                message: action.payload.message,
                email:'',
                token:'',
                oldPassword:'',
                success: action.payload.success,
                updated:false
            }
        default:
        return preState;
    }
} 

export default passwordReducer;