import React, { Component } from 'react';
import './style.css';
import {Link ,Redirect} from 'react-router-dom';




export default class NextOfKings extends Component {
    constructor(props){
        super(props);
        this.state = {
            skip: false,
        }
    }

   
    componentDidMount(){
        // if(!this.props.auth) return <Redirect to='/'/>

    }
    render() {
        let {
            errorMessage,
            handleSubmit,
            handleChange,
            msg,
            handleBackwardToSignup,
            nFirstName,
            nLastName,
            nEmail,
            nAddress,
            nMonth,
            nDay,
            nYear,
            nNumber,
            message,
            handleSkip
        } = this.props;


        // <<<<<<<<<<<<< FUTURE UPDATE >>>>>>>>>>
        
        if(this.props.auth && this.props.userInfo.setNextOfKin ) return <Redirect to='/signup/account'/>;
        if(!this.props.auth) return <Redirect to='/signup'/>;

        return (
            <div className='container mx-auto px-0 py-5'>
                <div className='row login-container mx-0'>
                    <form onSubmit={handleSubmit} className='m-auto login-form col-12 col-md-8 col-lg-5 pt-5 pb-4 row'>
                        <h3 className='signup-form text-center col-12 fadeIn'>Next of Kin Detail</h3>
                        <small className='text-danger text-center mb-3 d-block col-12'>{msg}</small>
                        <small className='text-danger text-center mb-3 d-block col-12'>{message}</small>

                        <div className='login-input-container col-12 col-md-6 mt-3'>
                            {/* <label for='userName mb-0'>User name</label> */}
                            <input value={nFirstName} name='nFirstName' placeholder='First name' onChange={handleChange} type='text' />
                            <span className='contact-error'>{errorMessage.nFirstName.length > 0?errorMessage.nFirstName:''}</span>
                        </div>
                        <div className='login-input-container col-12 col-md-6 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input value={nLastName} name='nLastName' placeholder='LastName' onChange={handleChange} type='text' />
                            <span className='contact-error'>{errorMessage.nLastName.length > 0?errorMessage.nLastName:''}</span>
                        </div>
                        <div className='login-input-container col-12 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input value={nAddress} name='nAddress' placeholder='Address'  onChange={handleChange} type='text' />
                            <span className='contact-error'>{errorMessage.nAddress.length > 0?errorMessage.nAddress:''}</span>
                        </div>
                        <div className='login-input-container col-12 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input value={nNumber} name='nNumber' placeholder='Phone number' onChange={handleChange} type='text' />
                            <span className='contact-error'>{errorMessage.nNumber.length > 0?errorMessage.nNumber:''}</span>
                        </div>
                        <div className='login-input-container col-12 mt-3'>
                            {/* <label for='userName mb-0'>User name</label> */}
                            <input value={nEmail} name='nEmail' placeholder='Email' onChange={handleChange} type='email' />
                            <span className='contact-error'>{errorMessage.nEmail.length > 0?errorMessage.nEmail:''}</span>
                        </div>
                        <div className='login-input-container col-12 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <select name='relationship' placeholder='Relationship' onChange={handleChange} className='small signup-select w-100' >
                                <option value='country'>Select Relationship</option>
                                <option value='ghana'>Bother</option>
                                <option value='togo'>Sister</option>
                                <option value='gongo'>wife</option>
                                <option value='gongo'>Husband</option>
                                <option value='gongo'>Son</option>
                                <option value='gongo'>Dauther</option>
                                <option value='gongo'>Nephew</option>
                                <option value='gongo'>Nece</option>
                                <option value='gongo'>Other</option>

                            </select>
                            <span className='contact-error'>{errorMessage.country.length > 0?errorMessage.country:''}</span>
                        </div>
                        <div className='login-input-container col-12 col-md-4 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input value={nDay} name='nDay' placeholder='Day' onChange={handleChange} type='days' />
                            <span className='contact-error'>{errorMessage.nDay.length > 0?errorMessage.nDay:''}</span>
                        </div>
                        <div className='login-input-container col-12 col-md-4 mt-3'>
                            {/* <label for='userName mb-0'>User name</label> */}
                            <input value={nMonth} name='nMonth' placeholder='Month' onChange={handleChange} type='month' />
                            <span className='contact-error'>{errorMessage.nMonth.length > 0?errorMessage.nMonth:''}</span>
                        </div>
                        <div className='login-input-container col-12 col-md-4 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input value={nYear} name='nYear' placeholder='Year' onChange={handleChange} type='years' />
                        </div>
                        <p className='text-black mt-5 text-center text-secondary xsmall mx-1 mx-md-3 mx-lg-4'> Please try to read the therm and condition before submiting your details to become a member of the group. <Link to='/terms_and_condition'>Read terms and condition</Link> and <Link to='/terms_and_condition'>Read privicy</Link></p>
                        <button onClick={handleBackwardToSignup} className='small login-submit bg-info mt-4 py-1 px-3 mx-auto text-rigth'>Back</button>
                        <button onClick={handleSkip} className='small login-submit bg-info mt-4 py-1 px-3 mx-auto text-rigth'>skip</button>
                        <button onSubmit={handleSubmit} className='small login-submit bg-info mt-4 py-1 px-3 mx-auto text-rigth'>Next</button>

                    </form>
                </div>
            </div>
        )
    }
}
