import React, { Component } from 'react';
import {connect} from 'react-redux';
import {sendTokenPlusPassword} from './../../../action/settings'
import {Redirect} from 'react-router-dom';


class PasswordReset extends Component {
    constructor(props){
        super(props);
        this.state = {
            password:'',
            comfirmPassword:'',
            errorMessage:{
                password:'',
                comfirmPassword:'',
                msg:''
            }
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }


    handleChange(e){
        e.preventDefault()
        let {name,value} = e.target
        let {errorMessage} = this.state
        switch (name) {
            case 'password':
                errorMessage.password = value.length === 10? '':'invalid token';
                break;
        
            default:
                break;
        }
        this.setState({[name]:value,errorMessage})
    }




    validateError = ()=>{
        let {errorMessage,password} = this.state;
        let isValid = true;

        Object.values(errorMessage).forEach(value =>{
            value.length > 0 && (isValid = false)  
        });
        Object.values({password}).forEach(value=>{
            value === '' && (isValid = false) 
        });
        return isValid;
    }


    handleSubmit(e){
        e.preventDefault()
        if(this.validateError() && this.state.password === this.state.comfirmPassword){
            let{token , email} = this.props;
            let credentials = {
                token,
                email,
                password: this.state.password
            };
            return this.props.sendTokenPlusPassword(credentials)
        }else{
            return this.setState({errorMessage:{msg:'passwords do not match'}});
        }
    }

    render(){
        let {errorMessage} = this.state
        let {message , token,auth} = this.props
        if(message.length || !token) return <Redirect to='/reset-password/token' />;
        if(auth) return <Redirect to='/dashboard' />;
        return(
            <div className='container mx-auto px-0'>
                <div className='row login-container mx-0'>
                    <form onSubmit={this.handleSubmit} className='m-auto login-form col-12 col-md-8 col-lg-5 pt-5 row pb-4 '>
                        <h3 className='signup-form text-center col-12 fadeIn'>Enter in your new password</h3>
                        <p className='small text-danger bold text-center mx-auto'>{this.state.errorMessage.msg}</p>
                        
                        <div className='password-input-container col-12 mt-3'>
                            {/* <label for='userName mb-0'>User name</label> */}
                            <input name='password' className={`${errorMessage.password.length > 0?'text-danger':''} form-control`} placeholder='Password' onChange={this.handleChange} type='password' />
                            <span className='contact-error'>{errorMessage.password.length > 0 ? errorMessage.password:''}</span>
                            <input name='comfirmPassword' className='form-control mt-3' placeholder='Comfirm password' onChange={this.handleChange} type='password' />
                            
                            <button onClick={this.handleSubmit} className='small login-submit bg-info mt-4 py-2 px-3 mx-auto text-rigth w-100' type='submit'>Submit</button>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

const mapStateToProps  = (state)=>{
    let isValid = state.auth.log === {} ? null : state.auth.log.success
    return{
        message:state.passwordReducer.message,
        email:state.passwordReducer.email,
        token:state.passwordReducer.token,
        auth: isValid,
    }
}

export default connect(mapStateToProps , {sendTokenPlusPassword})(PasswordReset)