import React, { Component } from 'react';
import Login from './Login';
import {loginUser,LoadUser} from './../../../action/auth'
import {connect} from 'react-redux';
import Redirect from 'react-router-dom/Redirect';


class LoginContainer extends Component {
    componentWillMount(){
        if(!this.props.auth){
            this.props.LoadUser()
        }
    }
    constructor(){
        super();
        this.state = {
            userName:"",
            password:"",
            errorMessage:{
                userName: '',
                password: ''
            }
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this)
    }
    handleChange(e){
        e.preventDefault();
        let {name , value} = e.target;
        let {errorMessage}  = this.state

        switch (name) {
            case 'userName':
                errorMessage.userName = value.length > 4?'': 'error invalid userName';
                break;
            case 'password':
                errorMessage.password = value.length > 6?'':'error invalid password';
                break;
            default:
                break;
        }
        return this.setState({[name]:value})
    }


    validateError = ()=>{
        let {errorMessage,userName,password} = this.state;
        let isValid = true;

        Object.values(errorMessage).forEach(value=>{
            value.length > 0 && (isValid = false) 
        });
        Object.values({userName,password}).forEach(value=>{
            value === '' && (isValid = false) 
        });
        return isValid
    }


    handleSubmit(e){
        e.preventDefault();

        if (this.validateError()) {
            this.setState({msg:""})
            let credentials = {
                userName:this.state.userName,
                password: this.state.password
            };
           return this.props.loginUser(credentials)
        }else{
           return this.setState({msg:"Incorrect username or password"})
        }
        
    }

    render() {
        if(this.props.auth && !this.props.userInfo.setNextOfKing ) return <Redirect to='/signup/nextofking'/>
        if(this.props.auth && this.props.userInfo.setNextOfKing) return <Redirect to='/dashboard'/>
        return (
            <div>
                <Login message = {this.props.message} handleChange={this.handleChange} handleSubmit={this.handleSubmit} {...this.state} />
            </div>
        )
    }
}

const mapStateToProps = (state)=>{
    let isValid = state.auth.log === {} ? null : state.auth.log.success 
    return{
        auth: isValid,
        updateError: Object.keys(state.errorReducer).length > 0 ? state.errorReducer: null,
        userInfo: isValid ? state.auth.log.response: null ,
        message: state.auth.message

    }
}

export default connect(mapStateToProps , {loginUser , LoadUser})(LoginContainer)