import React from 'react';
import { InputWithLable } from '../Template';


const NextOfKin = props => {
    let {
        lastName,
        firstName,
        email,
        number,
        address,
        residencialAddress,
        country,
        town,
        region,
        notEdit,
        handleChange,
        borderClass,
        handleToggleEdit,
        handleToggleEditCancel,

    } = props
    return (
        <div className='w-100  pb-5'>
            <div className='container mx-auto'>
                <div className='mt-4 d-flex'>
                    <button className='ml-auto btn btn-info' onClick={handleToggleEdit}>
                        {
                        notEdit? <i className='fa fa-edit '> Edit</i>: <i className='fa fa-save '> Save</i>    
                    }
                    </button>
                    {
                        !notEdit && <button className='ml-3 btn btn-danger' onClick={handleToggleEditCancel}><i className='fa fa-times'></i> Cancel</button>
                    }

                </div>
           
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={firstName} label='First Name' name='firstName' placeholder={'Enter First Name'}/>
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={lastName} label='Last Name' name='lastName' placeholder={'Enter Last Name'}/>
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={email} label='Email' name='email' placeholder={'Enter Email'}/>
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={address} label='Address' name='address' placeholder={'Enter Address'}/>
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={residencialAddress} label='Residencial Address ' name='residencialAddress' placeholder={'Enter Residencial Address'} />
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={number} label='Phone Number' name='number' placeholder={'Enter Phone Number'}/>
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={country} label='Country' name='country' placeholder={'Enter Country'} />
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={region} label='State/Region' name='region' placeholder={'Enter Your State'}/>
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={town} label='Town/Village' name='town' placeholder={'Enter Your Town'}/>


            </div>
        </div>
    )
}

export default NextOfKin