import {PROFILE_DATA_UPDATE_FAILD , CLEAR_ERROR} from './../type'



const errorReducer = ( preState={},action)=>{
    switch (action.type) {
        case PROFILE_DATA_UPDATE_FAILD:
            return {
                message: action.payload ,
                err: true
            };
        case CLEAR_ERROR:
            return {}
        default:
        return preState
    }
} 

export default errorReducer