import React from 'react'
import { Card } from '../Template'


const DashBoardPresentation = (props) => {
        let {
            email,
            userName,

            portal,
            level,
            number,
            accountNumber,
            bankName,
            branch,
            accountName,
            networkService,
            momoNumber,
            _id
        } = props

        
        return (
            <div>
             <div className='row' >
                    <div className='col-12 col-md-4'>
                        <Card text={'Personal Info'}>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>User Name:</span> {userName}</p>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>Email:</span> {email}</p>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>Phone:</span> {number}</p>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>User ID:</span> {_id}</p>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>Date Created:</span> {Date()}</p>

                            {/* <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>Sign ups:</span> {referrals.length}</p> */}

                            
                        </Card>
                    </div>
                    <div className='col-12 col-md-4'>
                        <Card text={'Account Info'}>
                            <p className='bold mp'>Mobile Money</p>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>Network Service:</span> {networkService}</p>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>MoMo Number:</span> {momoNumber}</p>
                            <p className='bold mp my-3'>Bank Detail</p>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>Paypal Account:</span> {email}</p>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>Bank Name:</span> {bankName}</p>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>Branch:</span> {branch}</p>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>Account Name:</span> {accountName}</p>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>Account Number:</span> {accountNumber}</p>

                        </Card>
                    </div>
                    <div className='col-12 col-md-4'>
                        <Card text={'Current Portal'}>
                        
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>Portal:</span> {portal}</p>
                            <p className='small text-secondary bold mb-1'><span className='large text-black'><i className='fa fa-envolop'></i>Level:</span> {level}</p>
                        </Card>
                    </div>
                </div>   
            </div>
        )
    }


    export default DashBoardPresentation