import React, { Component } from 'react'
import { Paragraph, AboutUsHeader,TextHeader } from '../../Template'
import {Link} from 'react-router-dom'

export default class JoinUs extends Component {
    render() {
        return (
            <div>
                <section className='pt-5'>
                    <AboutUsHeader text={'Join us'} />
                    <Paragraph isAboutUs={true} text={'Fasthelpers have a One-Time membership program that gives immediate result and offers learning, health care and earning potentials.'} />
                    <Paragraph isAboutUs={true} text={'Fasthelpers have available materials and training programmes that helps its members to acquire Entrepreneurial knowledge and skills that will help them to achieve success in the world of Entrepreneurship and Business.'} />
                    <Paragraph isAboutUs={true} text={'You can work from anywhere irrespective of where you are and what you do, members are given great training and team support in order for them to achieve success in their business.'} />
                    <Paragraph isAboutUs={true} text={'By the training and support given you, you will be able to achieve great success leading to a great Financial Freedom, Associate makes income for every enrollment they make within a week.'} />
                    <Paragraph isAboutUs={true} text={'We invite you to join us today, to become part of a bigger, likeminded community of achievers with an entrepreneurial spirit, who will acquire entrepreneurial knowledge and skills, and raise capital to fulfill their entrepreneurial dreams and goals, giving back to society in the process.'} />
                </section>
                <section className='pt-3'>
                    <TextHeader text={'WHO IS ELIGIBLE TO JOIN THIS CLUB?'}/> 
                    <hr/> 
                    <p>
                        <ol className='join-us-list'>
                            <li>Business Owners</li>
                            <li>Traders </li>
                            <li>Self employed people</li>
                            <li>Unemployed people [educated or uneducated]</li>
                            <li>Students</li>
                            <li>Pastors [Ministries/Fellowships]</li>
                            <li>Anyone looking for a genuine opportunity to transform his/her life.</li>
                        </ol>
                        <Link to='/signup' className='btn btn-primary'>Register Now</Link>
                    </p>

                </section>
            </div>
        )
    }
}
