import React, { Component } from 'react'
import './style.css'
export default class Contact extends Component {
    render() {
        let {handleChange,handleSubmit,name,errorMessage,email,message} = this.props
        return (
            <div className='row px-0'>
                <div className='mx-auto py-5 col-12 col-md-10 col-lg-8 px-4'>
                    <h1 className="contact-header">Get in touch</h1>
                    <p className='text-secondary arial small mb-0'>contact us today to find out how we can help you with your integrate needs</p>
                    <hr className='contact-hr'/>
                    <div className='row'>
                        <div className="col-12 col-md-6 col-lg-4"> 
                             <h3 className='small arial mb-3'><b>General Enquiries</b></h3>
                             <p className='mb-auto small text-info'>info.fasthelpers@gmail.com</p>
                            <p className='small text-secondary'>0547750394</p>
                        </div>
                        <div className="col-12 col-md-6 col-lg-4"> 
                             <h3 className='small arial mb-3'><b>Support</b></h3>
                             <p className='mb-auto small text-info'>contact.fasthelpers@gmail.com</p>
                            <p className='small text-secondary'>0244534085</p>
                        </div>
                        <div className="col-12 col-md-6 col-lg-4"> 
                             <h3 className='small arial mb-3'><b>Head Office Location</b></h3>
                            <p className='mb-auto small text-info'>Bakaano  Roundabout (Behind Simtek Computer School off the Ola Road near Bombay barbering before 19 Hauma Mansion 57)</p>
                             {/* <p className='small text-secondary'>+223 554 6690 43</p> */}
                        </div>
                    </div>
                    <hr/>
                    <div className='row'>
                        <div className='col-12 col-md-7 col-lg-7'>
                            <p className='large arial'>Send us a message</p>
                            <form className='row '>
                                <div className='mt-3 col-12'>
                                    <textarea  value={message} name='message' placeholder="Message: " onChange={handleChange} className={`${errorMessage.message.length > 0 ?'text-danger':''} contact-textarea`} rows='5'> </textarea>
                                    <span className='contact-error'>{errorMessage.message.length > 0 ?errorMessage.message: ''}</span>
                                </div>
                                <div className="mt-3 col-12 col-md-6">
                                    <input className={`${errorMessage.name.length > 0 ?'text-danger':''} contact-input small py-2 pl-2`} onChange={handleChange} placeholder='Your full name' name='name' value={name}/>
                                    <span className='contact-error'>{errorMessage.name.length > 0 ?errorMessage.name: ''}</span>
                                </div>
                                <div className="mt-3 col-12 col-md-6">
                                    <input className={`${errorMessage.email.length > 0 ?'text-danger':''} contact-input small py-2 pl-2`} onChange={handleChange} placeholder='Your email' name='email' value={email}/>
                                    <span className='contact-error'>{errorMessage.email.length > 0 ?errorMessage.email: ''}</span>
                                </div>
                                <div className="ml-auto col text-right">
                                    <button type='submit' onClick={handleSubmit} className='text-white contact-submit py-1 px-4 ml-auto small bg-info mt-3'>Send message</button>
                                </div>
                            </form>
                                
                        </div>
                        <div className='col-12 col-md-5 col-lg-5 bg-secondary'>
                            
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
