import {LOGIN_SUCCESSFUL , 
    SIGNUP_SUCCESSFUL, 
    LOGIN_FAILED, 
    SIGNUP_FAILED, 
    NEXT_OF_KING_FAILED, 
    NEXT_OF_KING_SUCCESS,
    ACCOUNT_SUCCESS,
    ACCOUNT_FAILED,
    ACCOUNT_LOAD_SUCCESS,
    ACCOUNT_LOAD_FAILED,
    NEXTOFKIN_LOAD_SUCCESS,
    NEXTOFKIN_LOAD_FAILED

    
} from './../type.js'
import {AUTH_COOKIE_NAME } from './../config/auth'
import axios from 'axios';

var now = new Date();
var time = now.getTime();
time += 3600 * 1000 * 24;
now.setTime(time);



export function newMember(credentials){
    return (dispatch) => {
        axios.post('/api/register', credentials).then((payload) => {
            if(!payload.data.success){
                return dispatch({
                    type: SIGNUP_FAILED,
                    payload:payload.data.message,
                })
            }
            // Setting Cookies
            // Encryped data from the API
            let encyptedData = payload.data.cookie
        
            // TODO : Must find a way to set Date ahead
            let expDate = now;
            // Cookies String And Setting Cookies
            let  cookie = `${AUTH_COOKIE_NAME}=${encyptedData}; expires=${expDate}`;
            document.cookie = cookie; 
            return dispatch({
                type: SIGNUP_SUCCESSFUL,
                payload: payload.data
            })

        }
        )
    }
}

export function registerNextOfKing(credentials){
    return (dispatch) => {
        axios.post('/api/register/nextOfKin' , credentials).then((payload)=>{
            if(!payload.data.success){
                return dispatch({
                    type: NEXT_OF_KING_FAILED,
                    payload: payload.data.message
                })
            }
            
            return dispatch({
                type: NEXT_OF_KING_SUCCESS,
                payload: payload.data
            })
        })
    }
}



export function registerAccount(credentials) {
    return (dispatch) => {
        axios.post('/api/register/account', credentials).then((payload) => {
            if (!payload.data.success) {
                return dispatch({
                    type: ACCOUNT_FAILED,
                    payload: payload.data.message
                })
            }

            return dispatch({
                type: ACCOUNT_SUCCESS,
                payload: payload.data
            })
        })
    }
}

export function loginUser(credentials){
    return (dispatch) => {
        axios.post('/api/login', credentials).then((payload) => {
            // check if login successful
            if(!payload.data.success){ 
                // set cooes
                // navigate to dashboard
                return dispatch({
                    type: LOGIN_FAILED,
                    payload:payload.data.message
                })
            }
            // Setting Cookies
            // Encryped data from the API
            let encyptedData = payload.data.cookie
            // TODO : Must find a way to set Date ahead
            let expDate = now;
;
            // Cookies String And Setting Cookies
            let  cookie = `${AUTH_COOKIE_NAME}=${encyptedData}; expires=${expDate}`;
            document.cookie = cookie; 
                      
            return dispatch({
                type: LOGIN_SUCCESSFUL,
                payload: payload.data
            })
        }
        )
    }
}



export function LoadUser(){
    // NOTE: LoadUser is a fuction that checks if a user is loged in
    // and if the user is loged in, load the user data
    return (dispatch)=>{
        if(document.cookie.length !== 0){
            let cookieDataArray = document.cookie.split('=');
            axios.post(`/api/userSignin?token=${cookieDataArray[1]}`).then((payload)=>{
                if(!payload.data.success){ 
                    // set cooes
                    // navigate to dashboard
                    return dispatch({
                        type: LOGIN_FAILED,
                        payload:{msg:'Invalid UserName or password'}
                    })
                }
                return dispatch({
                    type: LOGIN_SUCCESSFUL,
                    payload: payload.data
                })
            })
        }
    }
}

export function logOut(){
    return (dispatch)=>{
        if(document.cookie.length !== 0){
            let cookieDataArray = document.cookie.split('=');
            
            axios.post(`/api/logout?token=${cookieDataArray[1]}`).then((payload)=>{
                if(!payload.data.success){ 
                    // set cooes
                    // navigate to dashboard
                    return dispatch({
                        type: LOGIN_FAILED,
                        payload:{msg:'Logout Failed'}
                    })
                }
                let expDate = 'Thu jan 09 2020 07:54:37 GMT+0000 (Greenwich Mean Time)';
                let  cookie = `${AUTH_COOKIE_NAME}=${''}; expires=${expDate}`;
                
                document.cookie = cookie;
                return dispatch({
                    type: LOGIN_SUCCESSFUL,
                    payload: {}
                })
            })
        }
    }
}




export function LoadUserAccount() {
    // NOTE: LoadUser is a fuction that checks if a user is loged in
    // and if the user is loged in, load the user data
    return (dispatch) => {
        if (document.cookie.length !== 0) {
            let cookieDataArray = document.cookie.split('=');   
            axios.post(`/api/account?token=${cookieDataArray[1]}`).then((payload) => {
                if (!payload.data.success) {
                    // set cooes
                    // navigate to dashboard
                    return dispatch({
                        type: ACCOUNT_LOAD_FAILED,
                        payload: { msg: 'Invalid UserName or password' }
                    })
                }
                return dispatch({
                    type: ACCOUNT_LOAD_SUCCESS,
                    payload: payload.data
                })
            })
        }
    }
}




export function LoadUserNextOfKin() {
    // NOTE: LoadUser is a fuction that checks if a user is loged in
    // and if the user is loged in, load the user data
    return (dispatch) => {
        if (document.cookie.length !== 0) {
            let cookieDataArray = document.cookie.split('=');
            axios.post(`/api/nextofkin?token=${cookieDataArray[1]}`).then((payload) => {
                if (!payload.data.success) {
                    // set cooes
                    // navigate to dashboard
                    return dispatch({
                        type: NEXTOFKIN_LOAD_FAILED,
                        payload: { msg: 'Failed' }
                    })
                }
                return dispatch({
                    type: NEXTOFKIN_LOAD_SUCCESS,
                    payload: payload.data
                })
            })
        }
    }
}
