import React, { Component } from 'react'
import Contact from './Contact';
import {sendContactMail , closeMailResponse} from './../../../action/mails';
import {connect} from 'react-redux';
import SuccessMail from './SuccessMail';
import FailedMail from './FailedMail';







let emailRegx = RegExp(/^[A-z0-9.!#$%&'*+/=?^_`{|}~-]+@[A-z0-9-]+(?:\.[A-z0-9-]+)*$/);

class ContactContainer extends Component {
    constructor(){
        super()
        this.state = {
            name:'',
            email:'',
            message:'',
            errorMessage:{
                name:'',
                email:'',
                message:''
            }
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.closeResponse = this.closeResponse.bind(this)
    }
    handleChange(e){
        let {name,value} = e.target
        let {errorMessage} = this.state
        
        switch (name) {
            case 'name':
                errorMessage.name = value.length < 3? 'error at lease two(2) letters' : '';
                break;
            case 'email':
                errorMessage.email = emailRegx.test(value)? '':'error invalid email';
                break;
            case 'message':
                errorMessage.message = value.length < 6? 'error at lease two(6) letters' : '';
                break;
            default:
                break;
        }
        this.setState({[e.target.name]:e.target.value,errorMessage})
    }
    validateError = ()=>{
    let {errorMessage,name,message,email} = this.state;
    let isValid = true;

    Object.values(errorMessage).forEach(value=>{
        value.length > 0 && (isValid = false) 
    });
    Object.values({name,message,email}).forEach(value=>{
        value === '' && (isValid = false) 
    });
    return isValid
}

    handleSubmit(e){
        e.preventDefault();
        if(this.validateError()){
            let mail = {
                name : this.state.name,
                email: this.state.email,
                msg: this.state.message,
            }
            this.props.sendContactMail(mail)
            this.setState({name:'',email:'',message:''})
        }else{
            console.log('ERROR ');
        }

        
    }
    closeResponse(){
        return this.props.closeMailResponse()
    }
    render() {
        let {success , name} = this.props
        return (
            <div>
                <Contact  {...this.state} handleChange={this.handleChange} handleSubmit={this.handleSubmit}/>
                {
                    success && name && <SuccessMail name={name} closeResponse = {this.closeResponse}/>
                }
                {
                    !success && name && <FailedMail name={name} closeResponse = {this.closeResponse}/>
                }
            </div>
        )
    }
}

const mapStateToProps = (state)=>{
    return state.contactReducer
}

export default connect(mapStateToProps , {sendContactMail , closeMailResponse})(ContactContainer)