import React, { Component } from 'react';
import {resetPassword, sendToken} from './../../../action/settings';
import {Redirect} from 'react-router-dom';
import {connect} from 'react-redux'


class TokenSection extends Component {
    constructor(props){
        super(props);
        this.state = {
            token:'',
            errorMessage:{
                token:''
            }
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleResendToken = this.handleResendToken.bind(this);
    }


    handleChange(e){
        e.preventDefault()
        let {name,value} = e.target
        let {errorMessage} = this.state
        switch (name) {
            case 'token':
                errorMessage.token = value.length === 10? '':'invalid token';
                break;
        
            default:
                break;
        }
        this.setState({[name]:value,errorMessage})
    }




    validateError = ()=>{
        let {errorMessage,token} = this.state;
        let isValid = true;

        Object.values(errorMessage).forEach(value=>{
            value.length > 0 && (isValid = false) 
        });
        Object.values({token}).forEach(value=>{
            value === '' && (isValid = false) 
        });
        return isValid
    }


    handleSubmit(e){
        e.preventDefault()
        if(this.validateError()){
            let credentials = {
                email:this.props.email,
                token:this.state.token
            }
            this.props.sendToken(credentials)
            this.props.history.push('/reset-password/password')
        }
    }
    handleResendToken(e){
        e.preventDefault();
        return this.props.resetPassword({email:this.props.email})
    }

    render(){
        let {errorMessage} = this.state
        let {email , token , message} = this.props
        if(email && token) return <Redirect to='/reset-password/password' />
        if(!email || message.length > 0) return <Redirect to ='/reset-password'/>
        return(
            <div className='container mx-auto px-0'>
                <div className='row login-container mx-0'>
                    <form onSubmit={this.handleSubmit} className='m-auto login-form col-12 col-md-8 col-lg-5 pt-5 row pb-4 '>
                        <h3 className='signup-form text-center col-12 fadeIn'>Enter in the token sent to your email</h3>
                        <p className='small text-danger bold text-center mx-auto'>{this.props.message}</p>
                        <div className='password-input-container col-12 mt-3'>
                            {/* <label for='userName mb-0'>User name</label> */}
                            <input name='token' className={`${errorMessage.token.length > 0?'text-danger':''} form-control`} placeholder='TOKEN' onChange={this.handleChange} type='text' />
                            <span className='contact-error'>{errorMessage.token.length > 0 ? errorMessage.token:''}</span>
                            <button onClick={this.handleSubmit} className='small login-submit bg-info mt-4 py-2 px-3 mx-auto text-rigth w-100' type='submit'>Submit</button>
                        </div>
                        <p onClick={this.handleResendToken} className='text-secondary mt-3 ml-3 small resend-token'>Resend token</p>
                    </form>
                </div>
            </div>
        )
    }
}


const mapStateToProps  = (state)=>{
    return{
        message:state.passwordReducer.message,
        email:state.passwordReducer.email,
        token: state.passwordReducer.token
    }
}

export default connect(mapStateToProps , {resetPassword , sendToken})(TokenSection)