import axios from 'axios';
import {
    LOGIN_SUCCESSFUL,
    LOGIN_FAILED,
    SIGNUP_FAILED,
    SIGNUP_SUCCESSFUL,
    PROFILE_UPDATE
    } from './../type.js';
    
 export function loginIn(credentials){
    return(dispatch)=>{
        axios.post('http://localhost:9000',credentials).then((res , err)=>{
            if(err){
                return dispatch({
                    type: LOGIN_FAILED,
                    payload: err
                })
            }else if(res){
            return dispatch({
                type:LOGIN_SUCCESSFUL,
                payload:res,
            })
        }})
    }
 }

 export  function signup(credentials){
     return(dispatch)=>{
         axios.post('http://localhost:9000',credentials).then((res ,err)=>{
            if (err && err.message) {
                console.log(err && err.message);
                
            return dispatch({
                type: SIGNUP_FAILED,
                payload: err.err
            })
            }else{
            return dispatch({
                type:SIGNUP_SUCCESSFUL,
                payload:res,
            }) 
         }
        })
 }

 }

 export function updateProfile(obj) {
     return (dispatch)=>{
         dispatch({
             type: PROFILE_UPDATE,
             payload: obj
         })
     }
 }