import React, { Component } from 'react';
import {connect} from 'react-redux'
import AccountProfile from './AccountProfile.js'
import {LoadUserAccount} from './../../../../action/auth';
import {upadteUserAccount} from './../../../../action/settings'
import Redirect from 'react-router-dom/Redirect';

class AccountContainer extends Component {
    componentWillMount(props) {
        let { account } = this.props;
            // LOAD USER ACCOUNT INFO FROM THE DATABASE
            if(!account._id){
                this.props.LoadUserAccount()
            }   
    }
    componentDidMount() {
        this.setState({ ...this.props.account });
    }
 
    constructor(props) {
        super(props);
        this.state = {
            networkService: '',
            momoNumber: '',
            accountNumber: '',
            bankName: '',
            branch:'',
            accountName:'',
            notEdit: true,
            editImage: false,
            borderClass: 'myinput-border-none',
            ...this.props.isAuth
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleToggleEdit = this.handleToggleEdit.bind(this);
        this.handleToggleEditCancel = this.handleToggleEditCancel.bind(this);
    }
  
    handleChange(e) {
        e.preventDefault();
        let { name, value } = e.target;
        return this.setState({ [name]: value })
    }

    handleToggleEdit() {
        let {networkService ,momoNumber, accountNumber, bankName, branch,accountName } = this.state
        let update = {
            networkService,
            momoNumber,
            accountNumber,
            bankName,
            branch,
            accountName
        }
        this.state.notEdit ? this.setState({
            borderClass: '',
            notEdit: false,
            prevState: { ...this.state, borderClass: 'myinput-border-none', notEdit: true }
        }) : this.setState({
            borderClass: 'myinput-border-none',
            notEdit: true
        }, () => {
                return this.props.upadteUserAccount(update);
        })

    }
    handleToggleEditCancel() {
        this.setState({ ...this.state.prevState })
    }
    render() {
    if (!this.props.account._id)   return <Redirect to={'/dashboard/'}/>
    if (!this.props.auth) return <Redirect to={'/login'} /> 

        return (
            <div>
                <AccountProfile   
                handleToggleEditCancel={this.handleToggleEditCancel} 
                handleChange={this.handleChange} 
                handleToggleEdit={this.handleToggleEdit} {...this.state}/>
            </div>
        )
    }
}


const mapStateToProps =(state)=>{
    let account = state.AccountReducer.account
    let isValid = state.auth.log === {} ? null : state.auth.log.success    
    return{
        account,
        auth: isValid
    }
}

export default connect(mapStateToProps, { LoadUserAccount, upadteUserAccount})(AccountContainer);