import {
    NEXTOFKIN_LOAD_FAILED,
    NEXTOFKIN_LOAD_SUCCESS,
    NEXT_OF_KING_SUCCESS,
    NEXT_OF_KING_FAILED
} from './../type'

const myState = {
    nextOfKin:{

    },
    message:'',
}

const nextOfKinReducer = ( preState=myState,action)=>{
    const success = ''

     switch (action.type) {
        case NEXTOFKIN_LOAD_SUCCESS:
            return {
                nextOfKin: action.payload.response,
                message: success     
            }
         case NEXTOFKIN_LOAD_FAILED:
            return {
                nextOfKin: {},
                message: action.payload
            }
         case NEXT_OF_KING_SUCCESS:
             return {
                 nextOfKin: { ...preState.nextOfKin, ...action.payload},
                 message:success
             };
         case NEXT_OF_KING_FAILED:
            return {
                nextOfKin: { ...preState.nextOfKin},
                message: action.payload,
            }
         default:
         return preState
     }
 } 

 
export default nextOfKinReducer