import React, { Component } from 'react';
import Navbar from './Navbar';
import {connect} from 'react-redux';
import {logOut} from './../../../../action/auth';
import { LoadUserAccount, LoadUserNextOfKin} from './../../../../action/auth';


class DashboarNavbarContainer extends Component {
    componentWillMount(){
        if(!this.props.account._id){
            this.props.LoadUserAccount();
        }
        if(!this.props.nextOfKin._id){
            this.props.LoadUserNextOfKin();
        }
    }
    constructor(){
        super()
        this.state = {
            toggle:''
        }
        this.handleToggle = this.handleToggle.bind(this)
        this.LogOutHandler = this.LogOutHandler.bind(this);
    }
    handleToggle(){
        this.state.toggle === ''?this.setState({toggle:'d-block'}):this.setState({toggle:''})
    }

    LogOutHandler(){
        return this.props.logOut();
    }
    render() {
        return (
            <div>
                <Navbar {...this.props} {...this.state} handleToggle = {this.handleToggle} LogOutHandler={this.LogOutHandler} />
            </div>
        )
    }
}
const mapStateToProps = (state)=>{
    let isValid = state.auth.log === {} ? {} : state.auth.log.success
    return{
        userInfo: isValid ? state.auth.log.response : {},
        account: state.AccountReducer.account,
        nextOfKin: state.nextOfKinReducer.nextOfKin
    }
}
export default connect(mapStateToProps, { logOut, LoadUserAccount, LoadUserNextOfKin})(DashboarNavbarContainer)
