import React from 'react';
import { InputWithLable, InputDisable} from '../Template';
import image from './../../../../img/1.jpg';

const Profile = props => {
    let {
        lastName,
        firstName,
        email,
        phone,
        country,
        town,
        region,
        userName,
        notEdit,
        editImage,
        handleChange,
        borderClass,
        handleToggleEdit,
        handleToggleEditCancel,
        handleChangeImage,

    } = props
    return (
        <div className='w-100 pb-5 mb-5'>
            <div className='container mx-auto'>
                <div className='mt-4 d-flex'>
                    <button className='ml-auto btn btn-info' onClick={handleToggleEdit}>
                        {
                        notEdit? <i className='fa fa-edit '> Edit</i>: <i className='fa fa-save '> Save</i>    
                    }
                    </button>
                    {
                        !notEdit && <button className='ml-3 btn btn-danger' onClick={handleToggleEditCancel}><i className='fa fa-times'></i> Cancel</button>
                    }

                </div>
                <div className='row'>
                    <div className='col-md-4 col-12 d-flex'>
                    <img className='ml-auto rounded-circle' style={{width:'100px' , height:'100px'}} src={image} alt={'the user DP here'}/>
                    </div>
                    <div className='col-12 col-md-8 '>
                        <h3 className='mb-0'>George Arhinful</h3>
                        {
                            !editImage && <p><span onClick={handleChangeImage} className='profile-edit-pic text-info a'>Change picture</span></p>
                        }
                        {
                            editImage && <p><input type='file' className='' /></p>
                        }
                        
                        
                    </div>
                </div>
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={firstName} label='First Name' name='firstName' placeholder={'Enter First Name'}/>
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={lastName} label='Last Name' name='lastName' placeholder={'Enter Last Name'}/>
                <InputDisable   handleChange={handleChange} value={userName} label='User Name' name='userName' placeholder={'Enter User'} />
                <InputDisable   handleChange={handleChange} value={email} label='Email' name='email' placeholder={'Enter Email'}/>
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={phone} label='Phone Number' name='phone' placeholder={'Enter Phone Number'}/>
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={country} label='Country' name='country' placeholder={'Enter Your County'}/>
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={region} label='State/Region' name='region' placeholder={'Enter Your State'}/>
                <InputWithLable borderClass={borderClass} notEdit={notEdit} handleChange={handleChange} value={town} label='Town/Village' name='town' placeholder={'Enter Your Town'}/>


            </div>
        </div>
    )
}

export default Profile