import React, { Component } from 'react'
import './style.css'
import {Link} from 'react-router-dom'


export default class LinkIcon extends Component {
    render() {
        let {link,url,icon} = this.props 
        return (
            <Link to={`${url}`} className='dashboard-link mb-3'>
                <span className='icon-box mx-auto'>
                    <i className={`${icon} fa-1x m-auto`}></i>
                </span>
                <p className='text-center'>{link}</p>
            </Link>
        )
    }
}
