import React, { Component } from 'react'
import {connect} from 'react-redux';
import MyTeam from './MyTeam';
import {Redirect} from 'react-router-dom'
import { Card } from '../Template';
import {LoadUser} from './../../../../action/auth';


class MyteamContainer extends Component {
    componentDidMount(props){
        let {auth} = this.props;
        if(!auth){
            // LOAD USE INFO FROM THE DATABASE
            this.props.LoadUser()

            
        }
    }
    render() {
        let {
            userInfo,
            auth
        } = this.props
            if(!auth){
                return <Redirect to='/login'/>
        }
        return (
            <div className='myteam-container'>
                <Card text={'My Team'}>
                    {userInfo.referrals.length === 0 && <p className='text-secondary text-center'> No signups yet</p>}
                    <ul className='px-0'>
                        {
                            userInfo.referrals.map((associate , index)=>{
                                return  <MyTeam key={index} {...associate}/>
                            })
                        }
                    </ul>
                </Card>
            </div>
        )
    }
}

const mapStateToProps = (state , props)=>{
    let isValid = state.auth.log === {} ? null : state.auth.log.success 
    return{
        auth: isValid,
        userInfo: isValid ? state.auth.log.response: null
    }
}

export default connect(mapStateToProps, {LoadUser})(MyteamContainer)