import React, { Component } from 'react'
import './style.css';
import SideLinkContainer from './SideLink';
import DashboarNavbarContainer from './SideDisplay/Navbar';
import DashBoard from './SideDisplay/DashBoard';
import ProfileContainter from './SideDisplay/Profile';
import {Route} from 'react-router-dom';
import MyTeamContainer from './SideDisplay/MyTeam';
import PasswordRestContainer from './SideDisplay/Password';
import AccountContainer from './../BackOffice/SideDisplay/Account'
import NextOfKinContainter from './SideDisplay/NextOfKin';
import Portals from './SideDisplay/Portals';



export default class BackOffice extends Component {
    render() {
        return (
            <div>
                <div className='row dash-board-container bg-light px-0 px-md-3 mx-auto'>
                    <div className="d-none d-lg-block col-lg-2 bg-white ">
                       <SideLinkContainer/>  
                       
                    </div>
                    <div className='col-12 col-lg-10 px-0 px-md-3'>
                        <DashboarNavbarContainer/>
                        <Route path='/dashboard' component={DashBoard} exact/>
                        <Route path='/dashboard/portals' component={Portals} exact/>
                        <Route path='/dashboard/profile' component={ProfileContainter} exact/>
                        <Route path='/dashboard/myteam' component={MyTeamContainer} exact/>
                        <Route path='/dashboard/reset-password' component={PasswordRestContainer} exact/>
                        <Route path='/dashboard/account-update' component={AccountContainer} exact />
                        <Route path='/dashboard/nextofkin' component={NextOfKinContainter} exact />

                    </div>
                </div>
            </div>
        )
    }
}
