import React, { Component } from 'react'
import Slider from "react-slick";
import './style.css'


export default class NewsHeadContainer extends Component {
    render() {
    let settings = {
      dots: false,
      infinite: true,
      speed: 300,
      slidesToShow: 1,
      slidesToScroll: 1,
      vertical: true,
      autoplay:true,
      arrows:false,
     
    };
        return (
                <div className='container mx-auto row py-3 px-0'>
                    <div className='col-12 col-lg-8 py-3 mb-lg-0 mb-5'>
                        <div className='w-100'>
                            <p className=' mb-0 px-4 bg-primary my-auto header-news'><span className='my-auto'>News</span></p>
                            <div className='pl-4  bg-light w-100 new-detail'>
                                <Slider {...settings}>
                                    <div>
                                        <p className='mb-0'>Strategical Developmental Meeting 002</p>
                                    </div>
                                    <div>
                                        <p className='mb-0'>5pm to 6:30pm</p>
                                    </div>
                                    <div>
                                        <p className='mb-0'>A.M.E ZION CHURCH CAPE COAST</p>
                                    </div>
                                    <div>
                                        <p className='mb-0'>0558143748 / 02435666050 / 0547750394</p>
                                    </div>
                                    <div>
                                        <p className='mb-0'>THE MEGA 3W'S</p>
                                    </div>
                                    <div>
                                        <p className='mb-0'>FIRDAY 7th DEC.2018- 9am to 12noon</p>
                                    </div>
                                    <div>
                                        <p className='mb-0'>DOVE REVIVAL MINISTRY ELIMINA-JAVA HILL</p>
                                    </div>
                                    <div>
                                        <p className='mb-0'>024360719 / 0207216280 / 0545201614</p>
                                    </div>
                                </Slider>
                            </div>
                        </div>
                        <h3 className='text-center py-2 bg-light mt-3' >10002 membership</h3>
                    </div>
                    <div className='col-12 col-lg-4'>
                        <div className='h-100 d-flex'>
                            < iframe className='w-100 my-auto' src = "https://www.youtube.com/embed/Mtqugn1KiNo"
                            frameBorder = '0'
                            allow = 'autoplay; encrypted-media'
                            allowFullScreen
                            title = 'video' />
                        </div>
                    </div>
                </div>
        )
    }
}
