import {
    LOGIN_FAILED,
    LOGIN_SUCCESSFUL,
    SIGNUP_SUCCESSFUL,
    SIGNUP_FAILED,
    PROFILE_DATA_UPDATE,
    NEXT_OF_KING_FAILED,
    NEXT_OF_KING_SUCCESS,
    ACCOUNT_SUCCESS,
    ACCOUNT_FAILED
} from './../type.js'

let user = {
    log: {}
};



const successMessage = ''

export default function auth(state=user,action){

    switch (action.type) {
        case LOGIN_SUCCESSFUL:
            return {
                message: successMessage,
                log: action.payload
            }
        case LOGIN_FAILED:
            return {
                message: action.payload,
                log: {}
            }
        case SIGNUP_SUCCESSFUL:
            return{
                message: successMessage,
                log:action.payload
            }
        case SIGNUP_FAILED:
            return{
                message: action.payload,
                log: {}
            }
        case PROFILE_DATA_UPDATE:
            return {
                message: action.payload,
                log: {...state.log ,response:{...state.log.response,...action.payload} }
            }
            case NEXT_OF_KING_FAILED:
                return {
                    message: action.payload,
                    log: {...state.log ,response:{...state.log.response} }
                }
            case NEXT_OF_KING_SUCCESS:
                return {
                    message: successMessage,
                    log: {...state.log ,...action.payload} 
                }
            case ACCOUNT_FAILED:
                return {
                    message: action.payload,
                    log: { ...state.log, response: { ...state.log.response } }
                }
            case ACCOUNT_SUCCESS:
                return {
                    message: successMessage,
                    log: { ...state.log, ...action.payload }
                }
        default:
            return state
    }
}