import React, { Component } from 'react'
import Slider from "react-slick";
import Card from './Template.js'

export default class SmallSideContainer extends Component {
    render() {
        let settings = {
            dots: false,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            vertical: true,
            autoplay: true,
            arrows: false,
        };
        return (
            <div>
                <h1>
                    {/* <HeaderLink url={'/about-us'} text={'ADDS'} /> */}
                    <section>
                        <div>
                            <Slider {...settings}>
                                <div className='Card-list'>
                                    <Card name={'Rev. Albert Sam'} level={"Level 1 "} portal={"Portal One"}/>
                                </div>
                                <div className='Card-list'>
                                    <Card name={'Rev. Dr Edwin Ahene'} level={"Level 2"} portal={"Portal Two"} />
                                </div>
                                <div className='Card-list'>
                                    <Card name={'Apst. william K.Edametey'} level={"Level 3"} portal={"Portal One"} />
                                </div>
                                <div className='Card-list'>
                                    <Card name={'Yivian Esi Bruce Awotwe'} level={"Level"} portal={"Portal One"} />
                                </div>
                            </Slider>
                        </div>
                    </section>
                </h1>
            </div>
        )
    }
}
