import axios from 'axios';
import {CONTACT_MAIL_SENT , CONTACT_MAIL_SENT_FAILED , CLOSE_MESSAGE_RESPONSE} from './../type';

// Contact page mail handler


export const sendContactMail = (mail)=>{
    return (dispatch)=>{
        axios.post('/api/mail/contactUs',mail).then((res)=>{
            // console.log(res);
            if(res.data.success) return dispatch({
                    payload: {
                        success: res.data.success,
                        name: mail.name
                    },
                    type: CONTACT_MAIL_SENT
                })
            
            return dispatch({
                payload: {
                    success: false,
                    name: mail.name
                },
                type: CONTACT_MAIL_SENT_FAILED
            })
            })
    }
}

export const closeMailResponse = ()=>{
    return (dispatch)=>{
        dispatch({
            type: CLOSE_MESSAGE_RESPONSE
        })
    }
}
