import React, { Component } from 'react'
import './style.css'
import {Link} from 'react-router-dom'

export default class Navbar extends Component {
    render() {
        let {
            handleToggle,
            toggle,
            LogOutHandler
        } = this.props;
        return (
            <div className=''>
                <div className='col-12 bg-white pr-0 px-md-3'>
                    <nav className="navbar navbar-expand-lg navbar-light container mx-auto">
                         <span  className="navbar-brand">
                            <p className='navbar-brand-head mb-0'><i className='fa fa-user'></i> {`${this.props.userInfo ? this.props.userInfo.firstName : ''}  ${this.props.userInfo ? this.props.userInfo.lastName:''}`} </p>
                             <span className='navbar-brand-describtion text-info d-block'><span className=''>PERSONAL</span> , MANAGED</span>
                        </span>
                        <button onClick={handleToggle} className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className={`${toggle} collapse navbar-collapse`} id="navbarNav">
                            <ul className="navbar-nav">
                                <li className="nav-item">
                                    <Link to='/' className="nav-link">Home </Link>
                                </li>
                                <li className="nav-item">
                                    <Link to='/about-us' className="nav-link">About</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to='/howitworks' className="nav-link">How it works</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/join-us" className="nav-link">Join Us</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/" className="nav-link">Our Ethics</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/" className="nav-link">Policy</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/" className="nav-link">Terms of use</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/contact" className="nav-link">Contact us</Link>
                                </li>
                                {/* SIDE LINK TO DISPLAY WHEN THE SCREEN IS LESS THAN 900 px */}
                                
                                
                                <li className="d-lg-none nav-item">
                                    <Link to="/dashboard/profile" className="nav-link ">Personal Info</Link>
                                </li>
                                <li className="d-lg-none nav-item">
                                    <Link to="/dashboard/account-update" className="nav-link ">Account Info</Link>
                                </li>
                                <li className="d-lg-none nav-item">
                                    <Link to="/dashboard/nextofkin" className="nav-link ">Next Of Kin Info</Link>
                                </li>
                                <li className="d-lg-none nav-item">
                                    <Link to="/dashboard/reset-password" className="nav-link ">Password Info</Link>
                                </li>
                                <li className="d-lg-none nav-item">
                                    <Link to="/dashboard/portals" className="nav-link ">Portals</Link>
                                </li>
                                
                                {/* <<<<<<<<<<<<<<<<<<<END>>>>>>>>>>>>> SIDE LINK TO DISPLAY WHEN THE SCREEN IS LESS THAN 900 px */}


                                <li className="nav-item">
                                    <span onClick={LogOutHandler} className="nav-link btn btn-primary text-white">logout</span>
                                </li>
                            </ul>
                        </div>
                        </nav>
                </div>

            </div>
        )
    }
}
