import React, { Component } from 'react'
import './style.css';
import {Link} from 'react-router-dom';
import logo from './../../../img/logo2.png'

export default class Navbar extends Component {
    componentDidMount(){
        window.addEventListener('scroll', () => {
            if (window.scrollY > 71) {
                document.querySelector('#navbar-fixed-onScroll').style.position = 'fixed';
                document.querySelector('#navbar-fixed-onScroll').style.width = '100%';
                document.querySelector('#navbar-fixed-onScroll').style.border = '0px solid rgba(128, 128, 128, 0.63)';
                document.querySelector('#navbar-fixed-onScroll').style.boxShadow = ' 0px 0.5px 25px gray';
                document.querySelector('#navbar-fixed-onScroll').style.zIndex = '6';
                document.querySelector('#navbar-fixed-onScroll').style.top = '0px';
            } else {
                document.querySelector('#navbar-fixed-onScroll').style.position = 'static';
                document.querySelector('#navbar-fixed-onScroll').style.width = '100%';
                document.querySelector('#navbar-fixed-onScroll').style.borderBottom = '1px solid rgba(128, 128, 128, 0.63)';
                document.querySelector('#navbar-fixed-onScroll').style.boxShadow = ' 0px 0.5px 25px transparent';
                document.querySelector('#navbar-fixed-onScroll').style.backgroundColor = 'white';
                //    document.querySelector('#navbar-fixed-onScroll').style.top = '0px';
            }

        })
    }
    render() {
        let { handleToggle, toggle, auth, userInfo } = this.props;

        return (
            <div>
                <div className=''>
                    <nav className= 'bg-primary'>
                        <ul className='nav py-1 px-2 container mx-auto '>
                            
                            <Link className='d-none d-lg-block pl-3 bold text-black' to='/login'><span className='px-4'><img className='navbar-logo' src={logo} alt='fast helpers logo'></img></span></Link>
                            <li className='nav-item ml-sm-auto ml-md-auto'></li>
                            {
                                !auth && (<li className='nav-item ml-sm-auto ml-md-auto '><Link className='nav-link text-white small' to='/login'>Signin</Link></li>)
                            }
                            {
                                !auth && (<li className='nav-item'><Link className='nav-link text-white small' to='/signup'>Register</Link></li>)
                            }
                            <li className='nav-item d-flex navbar-search'>
                                <input type='text' placeholder='Search' className='nav-link'  name='search' />
                                <button type='search px-2'><i className='fa fa-search'></i></button>
                            </li>
                            {
                                auth && (<li className='nav-item d-flex'>
                                        <Link className='nav-link text-white ml-auto mr-2 pr-0' to='/dashboard'><span className='navbar-user-round'><i className='fa fa-user'></i></span></Link>
                                        <span>
                                            <span><b>{userInfo.firstName}</b></span><br/>
                                            <span><b>{userInfo.firstName}</b></span>

                                        </span>
                                    </li>)
                            }
                        </ul>
                    </nav>
                    <div id='navbar-fixed-onScroll' className='navbar-fixed-onScroll py-1'>
                    <nav className="navbar navbar-expand-lg navbar-light container mx-auto">

                         <Link to='/' className="navbar-brand d-block d-lg-none">FAST HELPERS</Link>
                        <button onClick={handleToggle} className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className={`${toggle} collapse navbar-collapse`} id="navbarNav">
                            <ul className="navbar-nav">
                                <li className="nav-item">
                                    <Link to='/'  className="nav-link">Home </Link>
                                </li>
                                <li className="nav-item">
                                    <Link to='/about-us' className="nav-link">About</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to='/howitworks' className="nav-link">How it works</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/join-us" className="nav-link">Join Us</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/" className="nav-link">Our Ethics</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/" className="nav-link">Policy</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/" className="nav-link">Terms of use</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/contact" className="nav-link">Contact us</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/signup" className="nav-link btn btn-primary text-light">Register Now</Link>
                                </li>
                            </ul>
                        </div>
                        </nav>
                    </div>
                        {/* <hr className='mt-auto'/> */}
                </div>
            </div>
        )
    }
}
