import React, { Component } from 'react';
import NextOfKin from './NextOfKin';
import {connect} from 'react-redux';
import {Redirect} from 'react-router-dom'
import { upadteUserNextOfKin} from './../../../../action/settings'
import {LoadUserNextOfKin} from './../../../../action/auth'



 class NextOfKinContainter extends Component {
    componentWillMount(props){
        let {nextOfKin} = this.props;
        if(!nextOfKin._id){
            // LOAD USE INFO FROM THE DATABASE
            this.props.LoadUserNextOfKin()
        }
    }
    componentDidMount(){
        this.setState({ ...this.props.nextOfKin});
    }

    constructor(props){
        super(props);
        this.state = {
            firstName:'',
            lastName:'',
            email: '',
            number: '',
            address:'',
            residencialAddress: '',
            country:'',
            region: '',
            town: '',
            notEdit:true,
            editImage: false,
            borderClass:'myinput-border-none',
            ...this.props.isAuth
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleToggleEdit = this.handleToggleEdit.bind(this);
        this.handleToggleEditCancel = this.handleToggleEditCancel.bind(this);
    }
    handleChange(e){
        e.preventDefault();
        let {name , value} = e.target;
    
        return this.setState({[name]:value})
    }

    handleToggleEdit(){
        let { firstName, lastName, email, number,address ,residencialAddress, country,region,town} = this.state
        let update = {
            firstName,
            lastName,
            email,
            number,
            country,
            region,
            town,
            address,
            residencialAddress
        }
        this.state.notEdit? this.setState({
            borderClass:'',
            notEdit: false,
            prevState: {...this.state,borderClass:'myinput-border-none',notEdit: true}}) :this.setState({
                borderClass:'myinput-border-none',
                notEdit: true},()=>{
                    return this.props.upadteUserNextOfKin(update);
        })
        
    }
    handleToggleEditCancel(){
        this.setState({...this.state.prevState})
    }
    render() {
        if(!this.props.auth) return <Redirect to={'/login'}/>

        return (
            <div className='px-3'>
                <NextOfKin handleChangeImage={this.handleChangeImage} {...this.state} handleChange={this.handleChange} handleToggleEdit={this.handleToggleEdit} handleToggleEditCancel={this.handleToggleEditCancel} />
            </div>
        )
    }
}



const mapStateToProps  = (state , props)=>{
    let isValid = state.auth.log === {} ? null : state.auth.log.success 
    return {
        auth: isValid,
        nextOfKin: state.nextOfKinReducer.nextOfKin
    }
}
export default connect(mapStateToProps, { upadteUserNextOfKin, LoadUserNextOfKin })(NextOfKinContainter)