import React from 'react'



const   MyTeam = (props)=>{
    let {
        firstName,
        lastName,

    } = props
            return (
                <li className='d-flex'>
                <span className=''><i className='fa fa-user pr-2'></i>{firstName} {lastName}</span>
                <small className='ml-auto text-secondary'>date: createOn</small>
                <small className='text-secondary ml-3'>level: 2</small>
                </li>
        )
    }

    export default MyTeam
