import React, { Component } from 'react'
import './style.css'
import {Link} from 'react-router-dom'

export default class Login extends Component {
    render() {
        let {userName,password,handleSubmit,handleChange,errorMessage,msg,message} = this.props
        return (
            <div className='container mx-auto px-0'>
                <div className='row login-container mx-0'>
                    <form onSubmit={handleSubmit} className='m-auto login-form col-12 col-md-8 col-lg-5 pt-5 '>
                        <h3 className='text-info text-center'>Sign In</h3>
                        <p className='small text-secondary text-center'>Check your level,team and news on your dashboard</p>
                        <p className='text-center text-danger'><small>{message} </small></p>
                        <small className='text-danger text-center mb-3 d-block '>
                            {msg} <br/>
                            {(msg && 'Enter in a valid Username and Password')}
                        </small>
                        <div className='login-input-container'>
                            {/* <label for='userName mb-0'>User name</label> */}
                            <input name='userName' className={`${errorMessage.userName.length > 0 ? 'text-danger':''}`} placeholder='User name' value={userName} onChange={handleChange} type='text' required/>
                            <span className='contact-error'>{errorMessage.userName}</span>
                        </div>
                        <div className='login-input-container mt-4'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input type='password' name='password' className={`${errorMessage.password.length > 0 ? 'text-danger':''}`} placeholder='Password' value={password} onChange={handleChange} required/>
                            <span className='contact-error'>{errorMessage.password}</span>
                        </div>
                        <button onClick={handleSubmit} className='small login-submit bg-info mt-5 py-2 px-4 ml-auto text-rigth w-100' type='submit'>Login</button>
                        <p className='small text-black mt-5 d-flex'>Don't have an account? <Link to='/signup' className='ml-1'> click to sign up</Link><Link className='text-right ml-auto' to='/reset-password'>forget password</Link></p>
                    </form>
                </div>
            </div>
        )
    }
}
