import React, { Component } from 'react';
import DashBoardPresentation from './DashBoard';
import {connect} from 'react-redux';
import {Redirect} from 'react-router-dom'
import {LoadUser} from './../../../../action/auth';


class DashBoard extends Component {
    componentWillMount(){
        let {auth} = this.props;
        if(!auth){
            // LOAD USE INFO FROM THE DATABASE
            this.props.LoadUser()
        }
    }
    render() {
        if(!this.props.auth){
            return <Redirect to={'/login'}/>
        } 
        return (
            <div>
                <DashBoardPresentation {...this.props.account} {...this.props.userInfo}/>
            </div>
        )
    }
}


const mapStateToProps  = (state)=>{
    let isValid = state.auth.log === {} ? null : state.auth.log.success
    let account = state.AccountReducer.account 
    return{
        auth: isValid,
        userInfo: isValid ? state.auth.log.response: null,
        account
    }
}

export default connect(mapStateToProps,{LoadUser})(DashBoard)