import React from 'react'
import './style.css'
import { Card } from '../Template'
import logo from './../../../../img/logo.jpg'
 


const PasswordRest = (props) => {
        let {
            oldPassword,
            newPassword,
            comfirmPassword,
            passwordAlert,
            isSetPassword,
            isSetPasswordNew,
            handleCancelPassword,
            handleChangePassword,
            handleSetPassword,
            handleChange,
            password,
            message
        } = props;
        
        return (
            <Card className='w-100 h-100' text='Reset Password'>
                <div className='password-reset-section py-5 position-relative' >
                    {
                        passwordAlert && (
                            <section>
                                <p className='text-secondary'>Do you want to change your password ?</p>
                                <div className='text-center'>
                                    <button onClick={handleChangePassword} className='btn btn-primary mx-2'>YES</button>
                                    <button onClick={handleCancelPassword} className='btn btn-danger mx-2'>NO</button>
                                </div> 
                            </section> 

                        )
                    }
                    {
                        isSetPassword &&  (
                            <section>
                                <form onSubmit={handleChangePassword}>
                                    { password.message.length >0 && <p className='text-center'> {password.message}</p>}
                                    <div>
                                        <label className='small bold'>Enter Old Password</label>
                                        <input onChange={handleChange} type='password' className='form-control text-secondary' name='oldPassword' value={oldPassword} placeholder='Password'/>
                                        <div className = 'text-right' >
                                            <button onSubmit={handleChangePassword} type='submit' className='mt-2 btn btn-info px-4'>Next</button>
                                        </div>
                                    </div>
                                </form>
                            </section>

                        )
                    }
                    {
                        isSetPasswordNew && !password.success && (
                            <section>
                                <form onSubmit={handleChangePassword}>
                                    {password.message.length > 0 && <p className='text-center text-danger'> {password.message}</p>}
                                    <div>
                                        <label className='small bold'>Enter Old Password</label>
                                        <input onChange={handleChange} type='password' className='form-control text-secondary' name='oldPassword' value={oldPassword} placeholder='Password' />
                                        <div className='text-right' >
                                            <button onSubmit={handleChangePassword} type='submit' className='mt-2 btn btn-info px-4'>Next</button>
                                        </div>
                                    </div>
                                </form>
                            </section>

                        )
                    }
                    {
                        isSetPasswordNew && password.success && (
                            <section>
                                <form onSubmit={handleSetPassword}>
                                    <div>
                                        {message.length > 0 && <p className='text-center text-danger'> {message}</p>}
                                        <label className='small bold'>Enter New Password</label>
                                        <input onChange={handleChange} type='password' className='form-control text-secondary' name='newPassword' value={newPassword} placeholder='Password'/>
                                        <input onChange={handleChange} type='password' className='form-control text-secondary my-2' name='comfirmPassword' value={comfirmPassword} placeholder='Comfirm Password'/>                                        
                                        <div className = 'text-right' >
                                            <button onSubmit={handleSetPassword} type='submit' className='mt-2 btn btn-info px-4'>Submit</button>
                                        </div>
                                    </div>
                                </form>
                            </section>

                        )
                    }
                    {
                        password.updated && (
                            <div className='position-absolute'>
                            <div className='bg-white p-5 text-center position-relative'>
                                <img alt='fasthelper logo' src={logo} className='mail-response-img' />
                                <h1 className='mail-response-header mt-3 text-success'>Password Reset Successful</h1>
                                {/* <p className='mb-0'>Hi your message has been recieved successfull.</p>
                                <p>you will recieve a response from us with in 24hrs, Thank you.</p> */}
                                    <span className='mail-response-close' onClick={handleCancelPassword} ><i className='fa fa-times'></i></span>
                            </div>
                            </div>
                        )
                    }
                </div>
            </Card>
        )
}

export default PasswordRest

