import React, { Component } from 'react';
import {Switch,Route} from 'react-router-dom'
import Pages from './components/Pages';
import BackOffice from './components/BackOffice';



class App extends Component {
 
  render() {
    return (
      <div>
        <Switch>
          <Route path='/' component={Pages} exact/>
          <Route path='/about-us' component={Pages} exact/>
          <Route path='/join-us' component={Pages} exact />
          <Route path='/howitworks' component={Pages} exact />


          <Route path='/login' component={Pages} exact />
          <Route path='/signup' component={Pages} exact />
          <Route path='/signup/account' component={Pages} exact />
          <Route path='/signup/nextofking' component={Pages} exact />

          <Route path='/contact' component={Pages} exact />    
          <Route path='/dashboard' component={BackOffice} exact/>
          <Route path='/dashboard/portals' component={BackOffice} exact />
          <Route path='/dashboard/profile' component={BackOffice} exact/>
          <Route path='/dashboard/myteam' component={BackOffice} exact/>
          <Route path='/reset-password' component={Pages} exact/>
          <Route path='/reset-password/token' component={Pages} exact/>
          <Route path='/reset-password/password' component={Pages} exact/>

          <Route path='/dashboard/reset-password' component={BackOffice} exact/>
          <Route path='/dashboard/account-update' component={BackOffice} exact/>
          <Route path='/dashboard/nextofkin' component={BackOffice} exact />

          

        </Switch> 
      </div>
    );
  }
}

export default App
