import React, { Component } from 'react'
import image from './../../../../img/1.jpg'

export default class Card extends Component {
    render() {
        return (
            <div className='bg-light p-5'>
                <img className='side-image-style mb-3' alt={this.props.name} src={image}/>
                <h5 className=''>{this.props.name}</h5>
                <p className=' text-secondary mb-0' style={{fontSize:'15px'}}>{this.props.portal}</p>
                <p className=' text-secondary' style={{ fontSize: '15px' }}>{this.props.level}</p>

            </div>
        )
    }
}
