import React, { Component } from 'react';
import logo from './../../../img/logo.jpg'


export default class FailedMail extends Component {
  render() {
    return (
        <div className='mail-response'>
        <div className='bg-white p-5 text-center position-relative'>
            <img alt='fasthelper logo' src={logo} className='mail-response-img' />
            <h1 className='mail-response-header mt-3 text-danger'>Mail Send Failed</h1>
              <p className='mb-0'>Hi {this.props.name}, your message didn't reach us .</p>
              <p>Please try to resend your message or send via your gmail, Thank you.</p>
              <span className='mail-response-close' onClick={this.props.closeResponse}><i className='fa fa-times'></i></span>
        </div>
    </div>
    );
  }
}
