import axios from 'axios';
import {
    LOGIN_SUCCESSFUL,
    PROFILE_DATA_UPDATE , 
    PROFILE_DATA_UPDATE_FAILD, 
    CLEAR_ERROR, TOKEN_MAIL_SENT,
    TOKEN_MAIL_SENT_FAiLED,
    TOKEN_SENT_SUCCESSFUL,
    PASSWORD_SET_FAILED,
    DASHBOARD_REST_UPDATED_SUCCESSFUL,
    DASHBOARD_REST_UPDATE_FAILED,
    DASHBOARD_REST_SUCCESSFUL,
    DASHBOARD_REST_FAILED,
    ACCOUNT_DATA_UPDATE,
    ACCOUNT_DATA_UPDATE_FAILD,
    NEXT_OF_KING_SUCCESS,
    NEXT_OF_KING_FAILED
} from './../type';
import {AUTH_COOKIE_NAME } from './../config/auth'


var now = new Date();
var time = now.getTime();
time += 3600 * 1000 * 24;
now.setTime(time);

export const upadteUserProfile = (update)=>{
    return (dispatch)=>{
        let cookieDataArray = document.cookie.split('=');
        
        return axios.post(`/api/settings/update/profile?token=${cookieDataArray[1]}`,update).then(res=>{          
            if(res.data.success) return dispatch({
                type: PROFILE_DATA_UPDATE,
                payload: update
            });
            return dispatch({
                type: PROFILE_DATA_UPDATE_FAILD,
                payload: "Update Failed" 
            });
        });
    };
};





export const clearError = ()=>{
    return (dispatch)=>{
        return dispatch({
            type: CLEAR_ERROR
        });
    };
};



export const resetPassword = credential =>{
    return dispatch =>{

        axios.post(`/api/passwordRest`,credential).then( data =>{
            if(data.data.success) return dispatch({
                type: TOKEN_MAIL_SENT,
                payload:{
                    message: '',
                    email: credential.email
                },
            });

            return dispatch({
                type: TOKEN_MAIL_SENT_FAiLED,
                payload:{
                    message:'Please resend your email.',
                }
            });
        });
       
    };
};

