export const resetPasswordDashboard = credential =>{
    return dispatch =>{

        axios.post(`/api/passwordRest/dashboard`,credential).then( data =>{
            console.log(data);
            
            if(data.data.success) return dispatch({
                type: DASHBOARD_REST_SUCCESSFUL,
                payload:{
                    message: '',
                    oldPassword: credential.oldPassword,
                    success: data.data.success,
                    updated: data.data.updated
                },
            });

            return dispatch({
                type: DASHBOARD_REST_FAILED,
                payload:{
                    message:data.data.message,
                    oldPassword: '',
                    success: data.data.success
                }
            });
        });
       
    };
};


export const resetPasswordDashboardUpdate = credential =>{
    return dispatch =>{

        axios.post(`/api/passwordRest/dashboard`,credential).then( data =>{
            if(data.data.success) return dispatch({
                type: DASHBOARD_REST_UPDATED_SUCCESSFUL,
                payload:{
                    message: data.data.message,
                    success: data.data.success
                },
            });

            return dispatch({
                type: DASHBOARD_REST_UPDATE_FAILED,
                payload:{
                    message:data.data.message,
                    success: data.data.success
                }
            });
        });
       
    };
};



export const sendToken = credential =>{
    return dispatch =>{
         return dispatch({
                type: TOKEN_SENT_SUCCESSFUL,
                payload:{
                    message: '',
                    email: credential.email,
                    token: credential.token,
                },
            });
    };
};
export const sendTokenPlusPassword = credential =>{
    return dispatch =>{
        axios.post(`/api/passwordRest?token=${credential.token}`,credential).then( data =>{
            if(data.data.success){
                // Setting Cookies
                // Encryped data from the API
                let encyptedData = data.data.cookie
                // TODO : Must find a way to set Date ahead
                let expDate = now;
                // Cookies String And Setting Cookies
                let  cookie = `${AUTH_COOKIE_NAME}=${encyptedData}; expires=${expDate}`;
                document.cookie = cookie; 
                        
                return dispatch({
                    type: LOGIN_SUCCESSFUL,
                    payload: data.data
                })
            }
            return dispatch({
                type: PASSWORD_SET_FAILED,
                payload:{
                    message:'invalid token , enter in a valide token',
                    email: credential.email,
                    token:null
                }
            });
        });
       
    };
};






