import React, { Component } from 'react';
import {Route} from 'react-router-dom';
import LoginContainer from './Login';
import SignupContainer from './Signup';
import ContactContainer from './Contact';
import FooterContainer from './Footer'
import NavContainer from './Navbar';
import NewsHeadContainer from './NewsHead';
import HomepageContainer from './Homepage';
import ForgotPassword from './Signup/ForgotPassword';
import TokenSection from './Signup/EnterToken';
import PasswordReset from './Signup/PasswordReset';



export default class Pages extends Component {
    render() {
        return (
            <div>
                <NavContainer/>
                <Route path='/' component={NewsHeadContainer} exact/>
                <Route path='/about-us' component={NewsHeadContainer} exact/>
                <Route path='/contact' component={NewsHeadContainer} exact/>
                <Route path='/howitworks' component={NewsHeadContainer} exact />

                <Route path='/' component={HomepageContainer} exact/>
                <Route path='/about-us' component={HomepageContainer} exact/>
                <Route path='/join-us' component={HomepageContainer} exact />
                <Route path='/howitworks' component={HomepageContainer} exact />


                <Route path='/contact' component={ContactContainer} exact />    
                <Route path='/' component={FooterContainer} exact/>
                <Route path='/contact' component={FooterContainer} exact/>
                <Route path='/about-us' component={FooterContainer} exact/>
                <Route path='/howitworks' component={FooterContainer} exact />
                <Route path='/join-us' component={FooterContainer} exact />




                <Route path='/login' component={LoginContainer} exact />
                <Route path='/signup' component={SignupContainer} exact /> 
                <Route path='/signup/account' component={SignupContainer} exact/>
                <Route path='/signup/nextofking' component={SignupContainer} exact/>
                
                <Route path='/reset-password' component={ForgotPassword} exact/>

                <Route path='/reset-password/token' component={TokenSection} exact/>
                <Route path='/reset-password/password' component={PasswordReset} exact/>

                

            </div>
        )
    }
}
