import {CONTACT_MAIL_SENT , CONTACT_MAIL_SENT_FAILED ,CLOSE_MESSAGE_RESPONSE} from './../type'


const myState = {
    success: null,
    name:null,
}

const contactReducer = ( preState=myState,action)=>{
     switch (action.type) {
         case CONTACT_MAIL_SENT:
             let {success , name} = action.payload
             return {
                 success,
                 name
             };
        case CONTACT_MAIL_SENT_FAILED:
            return {
                success: action.payload.success,
                name: action.payload.name
            }
        case CLOSE_MESSAGE_RESPONSE:            
            return {
                success: null,
                name: null
            }
         default:
         return preState
     }
 } 

 
 export default contactReducer