import React, { Component } from 'react';
import PasswordRest from './Password';
import {connect} from 'react-redux';
import {Redirect} from 'react-router-dom';
import { resetPasswordDashboard } from './../../../../action/settings';


class PasswordRestContainer extends Component {
    constructor(props){
        super(props);
        this.state = {
            oldPassword: '',
            newPassword: '',
            comfirmPassword: '',
            passwordAlert: true,
            isSetPassword: false,
            isSetPasswordNew: false,
            message:''
        }
        this.handleSetPassword= this.handleSetPassword.bind(this);
        this.handleChangePassword = this.handleChangePassword.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleCancelPassword = this.handleCancelPassword.bind(this)
    };

    handleCancelPassword(e){
        e.preventDefault();
        return this.props.history.goBack();
    }
    handleChangePassword(e){
        e.preventDefault();
        if(this.state.passwordAlert){
            this.setState({
                passwordAlert: false,
                isSetPassword: true
            })
            return
        } else if (!this.state.passwordAlert && !this.props.password.success){
                this.props.resetPasswordDashboard({
                oldPassword: this.state.oldPassword,
                id: this.props.userInfo._id
            })
            this.setState({ passwordAlert: false, isSetPassword: false, isSetPasswordNew: true })
        }else{
            this.props.resetPasswordDashboard({
                oldPassword: this.state.oldPassword,
                id: this.props.userInfo._id
            })
            this.setState({passwordAlert:false,isSetPassword:false,isSetPasswordNew: true})
        }

    }
    handleSetPassword(e) {
        e.preventDefault();
        if(this.state.newPassword === this.state.comfirmPassword && this.props.password.success){ 
            this.props.resetPasswordDashboard({
                id: this.props.userInfo._id,
                password: this.state.newPassword,
                success: this.props.password.success
            })
        } else if (this.state.newPassword !== this.state.comfirmPassword){
            this.setState({message:'Password does not match'})
        }else{
            this.props.history.goBack()
        }
    }

    
    handleChange(e){
        e.preventDefault();
        let {name , value} = e.target
        this.setState({[name]:value})
    }
    render() {
        
        if (!this.props.auth) return <Redirect to={'/login'} /> 
        // if (this.props.password.updated) return <Redirect to={'/dashboard'} /> 


        return (
            <div>
                <PasswordRest 
                {...this.state} 
                handleCancelPassword={this.handleCancelPassword}
                handleChangePassword={this.handleChangePassword}
                handleSetPassword={this.handleSetPassword}
                handleChange = {this.handleChange}
                {...this.props}
                />
            </div>
        )
    }
}
const mapStateToProps = (state)=>{
    let isValid = state.auth.log === {} ? null : state.auth.log.success
    return{
        auth: isValid,
        userInfo: state.auth.log.response,
        password: state.passwordReducer
    }
}

export default connect(mapStateToProps, { resetPasswordDashboard})(PasswordRestContainer)