import React, { Component } from 'react';
import './style.css';
import {Link , Redirect} from 'react-router-dom';
import {registerAccount } from './../../../action/auth';
import {connect} from 'react-redux';




class Account extends Component {
    constructor(){
        super()
        this.state = {
            payment:'',
            accountNumber:'',
            comfirmAccountNumber:'',
            errorMessage:{
                payment:'',
                accountNumber:'',
            }
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleSkip = this.handleSkip.bind(this);
    }
    handleChange(e){
        let {name , value} = e.target;
        let {errorMessage} = this.state ;
        switch (name) {
            case 'payment':
                // MORE VALIDATION
                errorMessage.payment = value !== 'payment' ? '' : 'select payment platform'
                break;
             case 'accountNumber':
                //  MORE VALIDATION
                errorMessage.accountNumber = value.length >=10 ? "":'invalid account number'
             break;
            default:
                break;
        }
        this.setState({[name]:value,errorMessage})
    }
    validateError = ()=>{
        let {errorMessage,accountNumber} = this.state;
        let isValid = true;

        Object.values(errorMessage).forEach(value=>{
            value.length > 0 && (isValid = false) 
        });
        Object.values({accountNumber}).forEach(value=>{
            value === '' && (isValid = false) 
        });
        return isValid
    }
    handleSkip(e){
        e.preventDefault();
        return this.props.registerAccount({ 
            userId: this.props.userInfo._id,
        });
    }
    handleSubmit(e){
        e.preventDefault();
        let {comfirmAccountNumber,accountNumber, payment} = this.state;
        if(accountNumber !== comfirmAccountNumber) return this.setState({msg: 'ERROR => ACCOUNT NUMBER DOES NOT MATCH.'})
        if(this.validateError() && accountNumber === comfirmAccountNumber){
            return this.props.registerAccount({
                accountNumber,
                payment,
                userId: this.props.userInfo._id
            });
        } 
        return this.setState({msg: 'ERROR => Please Complete the forms with a valid input.'})
    }



    render() {
        let {errorMessage} = this.state;
        if (this.props.auth && !this.props.userInfo.setNextOfKin ) return <Redirect to='/signup/nextofking'/>
        if (this.props.auth && this.props.userInfo.accountSet) return <Redirect to='/dashboard'/>
        console.log(this.props);

        return (
            <div className='container mx-auto px-0'>
                <div className='row login-container mx-0'>
                    <form onSubmit={this.handleSubmit} className='m-auto login-form col-12 col-md-8 col-lg-5 pt-5 row pb-4 '>
                        <h3 className='signup-form text-center col-12 fadeIn'>Account Detail</h3>
                        <p className='small text-center text-secondary col-12'>Hello, select the payment platform to be payed to</p>
                        <div className='login-input-container col-12  mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <select name='payment' placeholder='Year' onChange={this.handleChange} className='small w-100 signup-select' >
                                <option value='payment'>select payment portal</option>
                                <option value='mtn'>MTN Momo</option>
                                <option value='vodaphone'>Vodaphone Momo</option>
                                <option value='tigo'>Tigo Cash</option>
                                <option value='paypal'>pay pal</option>
                                <option value='payeer'>Payeer</option>
                                <option value='perfectmoney'>Perfect Money</option>
                                <option value='bitcoin'>Bit Coin</option>
                            </select>
                            <span className='contact-error'>{errorMessage.payment.length > 0? errorMessage.payment : ''}</span>
                        </div>
                        <div className='login-input-container col-12 mt-3'>
                            {/* <label for='userName mb-0'>User name</label> */}
                            <input name='accountNumber' placeholder='Account Number' onChange={this.handleChange} type='password' />
                            <span className='contact-error'>{errorMessage.accountNumber.length > 0? errorMessage.payment : ''}</span>
                        </div>
                        <div className='login-input-container col-12 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input name='comfirmAccountNumber' placeholder='Comfirm Account Number' onChange={this.handleChange} type='password' />
                        </div>
                        <p className='text-black mt-5 text-center text-secondary xsmall mx-1 mx-md-3 mx-lg-4 shake'> Please try to read the therm and condition before submiting your details to become a member of the group. <Link to='/terms_and_condition'>Read terms and condition</Link> and <Link to='/terms_and_condition'>Read privicy</Link></p>
                        <button onClick={this.props.handleBackward} className='small login-submit bg-info mt-4 py-1 px-3 mx-auto text-rigth' type='submit'>Back</button>                        
                        <button onClick={this.handleSkip} className='small login-submit bg-info mt-4 py-1 px-3 mx-auto text-rigth' type='submit'>Skip</button>
                        <button onClick={this.handleSubmit} className='small login-submit bg-info mt-4 py-1 px-3 mx-auto text-rigth' type='submit'>Register</button>
                    </form>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state)=>{
    let isValid = state.auth.log === {} ? {} : state.auth.log.success 
    return{
        auth: isValid,
        updateError: Object.keys(state.errorReducer).length > 0 ? state.errorReducer : {},
        userInfo: isValid ? state.auth.log.response : {},
        message: state.auth.message
    }
}
export default connect(mapStateToProps,{registerAccount})(Account)