import React, { Component } from 'react'
import './style.css';
import {
    Paragraph,
    AboutUsHeader,
    TextHeader
} from '../../Template';

export default class AboutUs extends Component {
    render() {
        return (
            <div className='pb-5 pt-3'>
                <section>
                    <AboutUsHeader text={'FastHelpers Group [Association] Global'}/>
                    
                    <Paragraph isAboutUs={true} text={'Fast Helpers Global Group is a Financial Networking System, Health Care and Training group which has been created to help people acquire financial freedom and good health care and to become entrepreneurs, helping them to raise capital to start their own businesses through the power of networking.'}/>
                    
                    <Paragraph isAboutUs={true} text={'We believe in creating business opportunities by coaching, raising and empowering young men and women to become Entrepreneurs using the power of training and networking.'}/>
                    
                    <Paragraph isAboutUs={true} text={'It is out of this vision that Fast Helpers has been born to help people from all walks of life, use this system as a springboard to acquire necessary knowledge and skills to become entrepreneurs and raise the needed capital to pursue their dreams and goals.'}/>
                    
                </section> 
                <section>
                    <TextHeader text={'Our Mission'} />
                    <Paragraph isAboutUs={true} text={'Raising ordinary people to become financially empowered through financial literacy and the creation of wealth with the aid of a credible and trusted system, and working together as one with a common goal.'} />
                    <TextHeader text={'Our Vision'} />
                    <Paragraph isAboutUs={true} text={'To become a leading association in Ghana and beyond, ensuring financial independence of its members in the near future, and relying on committed leaders, motivated members and a trusted system.'} />

                </section>
                <section className='pt-5'>
                    <AboutUsHeader text={'Join us'}/>
                    <Paragraph isAboutUs={true} text={'Fasthelpers have a One-Time membership program that gives immediate result and offers learning, health care and earning potentials.'}/>
                    <Paragraph isAboutUs={true} text={'Fasthelpers have available materials and training programmes that helps its members to acquire Entrepreneurial knowledge and skills that will help them to achieve success in the world of Entrepreneurship and Business.'} />
                    <Paragraph isAboutUs={true} text={'You can work from anywhere irrespective of where you are and what you do members are given great training and team support in order for them to achieve success in their business.'} />
                    <Paragraph isAboutUs={true} text={'By the training and support given you, you will be able to achieve great success leading to a great Financial Freedom, Associate makes income for every enrollment they make within a week.'} />
                    <Paragraph readMore={true} url={'/join-us'} isAboutUs={true} text={'We invite you to join us today, to become part of a bigger, likeminded community of achievers with an entrepreneurial spirit, who will acquire entrepreneurial knowledge and skills, and raise capital to fulfill their entrepreneurial dreams and goals, giving back to society in the process.'} />
                </section>
                
            </div>
        )
    }
}
