import {ACCOUNT_DATA_UPDATE , ACCOUNT_DATA_UPDATE_FAILD ,ACCOUNT_LOAD_FAILED, ACCOUNT_LOAD_SUCCESS} from './../type'

const myState = {
    account:{

    },
    message:'',
}

const AccountReducer = ( preState=myState,action)=>{
    const success = ''

     switch (action.type) {
        case ACCOUNT_LOAD_SUCCESS:
            return {
                account: action.payload.response,
                message: success     
            }
         case ACCOUNT_LOAD_FAILED:
            return {
                account: {},
                message: action.payload
            }
         case ACCOUNT_DATA_UPDATE:
             return {
                 account: { ...preState.account, ...action.payload},
                 message:success
             };
         case ACCOUNT_DATA_UPDATE_FAILD:
            return {
                account: { ...preState.account},
                message: action.payload,
            }
         default:
         return preState
     }
 } 

 
export default AccountReducer