import React, { Component } from 'react';
import logo from './../../../img/logo.jpg'

class SuccessMail extends Component {
  render() {
    return (
      <div className='mail-response'>
          <div className='bg-white p-5 text-center position-relative'>
              <img alt='fasthelper logo' src={logo} className='mail-response-img' />
              <h1 className='mail-response-header mt-3 text-success'>Mail Send Success</h1>
                <p className='mb-0'>Hi {this.props.name}, your message has been recieved successfull.</p>
                <p>you will recieve a response from us with in 24hrs, Thank you.</p>
                <span className='mail-response-close' onClick={this.props.closeResponse}><i className='fa fa-times'></i></span>
          </div>
      </div>
    );
  }
}


export default SuccessMail