import React, { Component } from 'react';
import './style.css';
import {Link ,Redirect} from 'react-router-dom';




export default class Signup extends Component {
    render() {
        let {message,errorMessage,handleSubmit,handleChange,msg , firstName, lastName,town,region,email,number,comfirmPassword,password,userName} = this.props
        if(this.props.auth && !this.props.userInfo.setNextOfKing ) return <Redirect to='/signup/nextofking'/>
        if(this.props.auth && this.props.userInfo.setNextOfKing) return <Redirect to='/'/>
        return (
            <div className='container mx-auto px-0 py-5'>
                <div className='row login-container mx-0'>
                    <form onSubmit={handleSubmit} className='m-auto login-form col-12 col-md-8 col-lg-5 pt-5 row'>
                        <h3 className='signup-form text-center col-12 fadeIn'>Personnal Detail</h3>
                        <small className='text-danger text-center mb-3 d-block col-12'>{msg}</small>
                        <small className='text-danger text-center mb-3 d-block col-12'>{message}</small>

                        <div className='login-input-container col-12 col-md-6 mt-3'>
                            {/* <label for='userName mb-0'>User name</label> */}
                            <input value={firstName} name='firstName' placeholder='First name' onChange={handleChange} type='text' />
                            <span className='contact-error'>{errorMessage.firstName.length > 0?errorMessage.firstName:''}</span>
                        </div>
                        <div className='login-input-container col-12 col-md-6 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input value={lastName} name='lastName' placeholder='LastName' onChange={handleChange} type='text' />
                            <span className='contact-error'>{errorMessage.lastName.length > 0?errorMessage.lastName:''}</span>
                        </div>
                        <div className='login-input-container col-12 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input value={userName} name='userName' placeholder='User Name' onChange={handleChange} type='text' />
                            <span className='contact-error'>{errorMessage.userName.length > 0 ? errorMessage.userName : ''}</span>
                        </div>
                        <div className='login-input-container col-12 mt-3 col-md-6'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input value={town} name='town' placeholder='Town'  onChange={handleChange} type='text' />
                            <span className='contact-error'>{errorMessage.town.length > 0?errorMessage.town:''}</span>
                        </div>
                        <div className='login-input-container col-12 col-md-6 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input value={region} name='region' placeholder='Region' onChange={handleChange} type='text' />
                            <span className='contact-error'>{errorMessage.region.length > 0?errorMessage.region:''}</span>
                        </div>
                        <div className='login-input-container col-12 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <select name='country' placeholder='Year' onChange={handleChange} className='small signup-select w-100' >
                                <option value='country'>Select country</option>
                                <option value='ghana'>Ghana</option>
                                <option value='togo'>Togo</option>
                                <option value='gongo'>Gongo</option>
                            </select>
                            <span className='contact-error'>{errorMessage.country.length > 0?errorMessage.country:''}</span>
                        </div>
                        <div className='login-input-container col-12 col-md-7 mt-3'>
                            {/* <label for='userName mb-0'>User name</label> */}
                            <input value={email} name='email' placeholder='email' onChange={handleChange} type='email' />
                            <span className='contact-error'>{errorMessage.email.length > 0?errorMessage.email:''}</span>
                        </div>
                        <div className='login-input-container col-12 col-md-5 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input value={number} name='number' placeholder='Phone number' onChange={handleChange} type='text' />
                            <span className='contact-error'>{errorMessage.number.length > 0?errorMessage.number:''}</span>
                        </div>
                        <div className='login-input-container col-12 col-md-6 mt-3'>
                            {/* <label for='userName mb-0'>User name</label> */}
                            <input value={password} name='password' placeholder='password' onChange={handleChange} type='password' />
                            <span className='contact-error'>{errorMessage.password.length > 0?errorMessage.password:''}</span>
                        </div>
                        <div className='login-input-container col-12 col-md-6 mt-3'>
                            {/* <label for='password mb-0'>Password</label> */}
                            <input value={comfirmPassword} name='comfirmPassword' placeholder='Comfirm password' onChange={handleChange} type='password' />
                        </div>
                        <p className='text-black mt-5 text-center text-secondary xsmall mx-1 mx-md-3 mx-lg-4'> Please try to read the therm and condition before submiting your details to become a member of the group. <Link to='/terms_and_condition'>Read terms and condition</Link> and <Link to='/terms_and_condition'>Read privicy</Link></p>
                        <button onSubmit={handleSubmit} className='small login-submit bg-info mt-4 py-1 px-3 mx-auto text-rigth'>Submit</button>
                        <p className='small text-black col-12 mt-4'> Have an account? <Link to='/login'>click to logins</Link></p>
                    </form>
                </div>
            </div>
        )
    }
}
