import React, { Component } from 'react'
import LargeSideContainer from './largeSide'
import SmallSideContainer from './smallSide'
import './style.css';
import AboutUs from '../AboutUs';
import {Route} from 'react-router-dom';
import JoinUs from '../JoinUs';
import HowItWorks from '../HowItWorks';

export default class HomepageContainer extends Component {
    render() {
        return (
            <div>
                <div className='container mx-auto row pb-5 px-0'>
                    <div className='col-12 col-lg-8'>
                        <Route path='/' component={LargeSideContainer} exact/>
                        <Route path='/about-us' component={AboutUs} exact/>
                        <Route path='/join-us' component={JoinUs} exact />
                        <Route path='/howitworks' component={HowItWorks} exact/>

                    </div>
                    <div className='col-12 col-lg-4'>
                        <SmallSideContainer/>
                    </div>
                </div>   
            </div>
        )
    }
}
