import React from 'react'
import { CardForPortal } from '../Template'

const PortalPresentation = (props) => {  
        return (
            <div>
             <div className='row' >
                    <div className='col-12 col-md-4'>
                        <CardForPortal text={'Portal One'} recycle={'0'}>
                            
                        </CardForPortal>
                    </div>
                    <div className='col-12 col-md-4'>
                        <CardForPortal text={'Portal Two'} recycle={'0'}>
                            
                        </CardForPortal>
                    </div>
                    <div className='col-12 col-md-4'>
                        <CardForPortal text={'Portal Three'} recycle={'0'}>
                            
                        </CardForPortal>
                    </div>
                </div>   
            </div>
        )
    }


export default PortalPresentation